<template>
  <v-container class="py-2">
    <!-- Cart Progress -->
    <div class="d-flex mb-6 px-0" style="justify-content: space-between">
      <div class="d">
        <h3 class="pt-2">Shopping Cart</h3>
        <span style="display: block" class="text-justify pt-2">
          <span style="font-weight: 600">Total Items: </span>{{ totalItems }}
        </span>
      </div>

      <v-btn icon variant="outlined" style="border: none" class="mx-2">
        <v-badge :content="CartItem.length" color="red" overlap>
          <v-icon color="green" size="40">mdi-cart</v-icon>
        </v-badge>
      </v-btn>
    </div>

    <!--    -->
    <v-row v-if="!CartItem.length" justify="center">
      <v-col cols="12" md="6" class="text-center">
        <v-icon size="80">mdi-cart-off</v-icon>
        <h2 class="mt-4">Your cart is empty</h2>
        <div class="pt-10">
          <v-btn
            variant="outlined"
            @click="drawer = false"
            style="border-radius: 10px"
            >Containue shopping</v-btn
          >
        </div>
      </v-col>
    </v-row>

    <!--    -->
    <v-row v-else>
      <!--  -->
      <v-col cols="12">
        <v-card v-for="item in CartItem" :key="item.id" class="mb-4 pa-4">
          <v-row align="center">
            <!--  -->
            <v-col cols="4">
              <v-img :src="item.thumbnail" height="100" cover />
            </v-col>

            <!--  -->
            <v-col cols="8">
              <h4 style="font-size: 12px" class="text-justify">
                {{ item.title }}
              </h4>
              <p class="text-grey text-justify pt-2">
                Price: ${{ item.price }}
              </p>
            </v-col>

            <!--  -->
            <v-col cols="12">
              <v-row>
                <v-col cols="9" class="d-flex align-center">
                  <v-btn icon @click="decrease(item)">
                    <v-icon>mdi-minus</v-icon>
                  </v-btn>

                  <span class="mx-3">{{ item.quantity }}</span>

                  <v-btn icon @click="increase(item)">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </v-col>

                <!--  -->
                <v-col cols="3" class="text-right">
                  <v-btn
                    icon
                    size="35"
                    color="red"
                    @click="removeItem(item.id)"
                  >
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-col>

      <!--   -->
      <v-col cols="12">
        <v-card class="pa-6">
          <h3 class="text-justify">Order Summary</h3>

          <div class="d-flex justify-space-between my-3">
            <span>Total Items:</span>
            <span>{{ totalItems }}</span>
          </div>

          <div class="d-flex justify-space-between my-3">
            <span>Total Price:</span>
            <span>${{ totalPrice }}</span>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script setup>
import { computed, watch } from "vue";
import { AddInCart } from "@/store/Cart";

const cartStore = AddInCart();
const CartItem = cartStore.CartItem;

//  
const increase = (item) => {
  item.quantity++;
  localStorage.setItem("cart-product", JSON.stringify(CartItem));
};

//  
const decrease = (item) => {
  if (item.quantity > 1) {
    item.quantity--;
    localStorage.setItem("cart-product", JSON.stringify(CartItem));
  }
};

//  
const removeItem = (id) => {
  cartStore.removeFromCart(id);
};

//   
const totalItems = computed(() =>
  CartItem.reduce((acc, item) => acc + item.quantity, 0)
);

//  
const totalPrice = computed(() =>
  CartItem.reduce((acc, item) => acc + item.price * item.quantity, 0)
);

//  localStorage   
watch(
  () => CartItem,
  (newVal) => {
    localStorage.setItem("cart-product", JSON.stringify(newVal));
  },
  { deep: true }
);
</script>

<style scoped>
/*     */
.v-badge__badge {
  font-weight: bold;
  font-size: 14px;
}
body {
  font-size: 12px !important;
}
</style>
