<template>
  <div class="cart-page bg-grey-lighten-4 py-10" style="min-height: 100vh">
    <v-container>
      <v-row>
        <v-col cols="12">
          <h2 class="text-h4 font-weight-bold mb-5 text-grey-darken-3">
            {{
              isCheckout
                ? "Checkout & Payment"
                : `Shopping Cart (${cartItems.length})`
            }}
          </h2>
        </v-col>
      </v-row>

      <v-row
        v-if="cartItems.length === 0"
        justify="center"
        class="text-justify py-10"
      >
        <v-col cols="12" md="6">
          <v-icon size="100" color="grey-lighten-1">mdi-cart-outline</v-icon>
          <h3 class="text-h5 text-grey mt-4">Your cart is empty</h3>
          <p class="text-body-1 text-grey mb-6">
            Looks like you haven't added anything yet.
          </p>
          <v-btn
            color="black"
            to="/"
            height="50"
            class="px-10 rounded-pill"
            flat
          >
            Continue Shopping
          </v-btn>
        </v-col>
      </v-row>

      <v-row v-else>
        <v-col cols="12" md="8">
          <div v-if="!isCheckout">
            <v-card
              v-for="item in cartItems"
              :key="item.id"
              class="mb-4 pa-4 rounded-lg"
              elevation="0"
              border
            >
              <v-row align="center">
                <v-col cols="4" sm="2">
                  <v-img
                    :src="item.thumbnail"
                    aspect-ratio="1"
                    cover
                    class="rounded-lg bg-grey-lighten-2"
                  ></v-img>
                </v-col>

                <v-col cols="8" sm="4">
                  <div
                    class="text-subtitle-1 font-weight-bold text-truncate text-justify"
                  >
                    {{ item.title }}
                  </div>
                  <div class="text-caption text-grey text-justify">
                    {{ item.brand }}
                  </div>
                  <div class="text-body-2 mt-1 text-justify">
                    Price: <strong>${{ item.price }}</strong>
                  </div>
                </v-col>

                <v-col
                  cols="12"
                  sm="3"
                  class="d-flex align-center justify-start justify-sm-center mt-3 mt-sm-0"
                >
                  <div
                    class="qty-control d-flex align-center border rounded-pill px-2 py-1"
                  >
                    <v-btn
                      icon="mdi-minus"
                      variant="text"
                      density="compact"
                      size="small"
                      @click="updateQuantity(item, -1)"
                      :disabled="item.quantity <= 1"
                    ></v-btn>
                    <span class="mx-3 font-weight-bold">{{
                      item.quantity
                    }}</span>
                    <v-btn
                      icon="mdi-plus"
                      variant="text"
                      density="compact"
                      size="small"
                      @click="updateQuantity(item, 1)"
                    ></v-btn>
                  </div>
                </v-col>

                <v-col
                  cols="12"
                  sm="3"
                  class="d-flex align-center justify-space-between justify-sm-end mt-3 mt-sm-0"
                >
                  <div class="text-subtitle-1 font-weight-bold mr-4">
                    ${{ (item.price * item.quantity).toFixed(2) }}
                  </div>
                  <v-btn
                    icon="mdi-delete-outline"
                    variant="text"
                    color="red"
                    @click="removeItem(item.id)"
                  ></v-btn>
                </v-col>
              </v-row>
            </v-card>
          </div>

          <div v-else>
            <v-btn
              variant="text"
              prepend-icon="mdi-arrow-left"
              @click="isCheckout = false"
              class="mb-4"
            >
              Back to Cart
            </v-btn>

            <v-card class="pa-6 rounded-lg mb-4" elevation="0" border>
              <h3 class="text-h6 font-weight-bold mb-4 d-flex align-center">
                <v-icon color="black" class="mr-2"
                  >mdi-truck-delivery-outline</v-icon
                >
                Shipping Details
              </h3>
              <v-row>
                <v-col cols="12" md="6">
                  <v-text-field
                    label="Full Name"
                    v-model="shippingInfo.name"
                    variant="outlined"
                    density="comfortable"
                    hide-details="auto"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    label="Phone Number"
                    v-model="shippingInfo.phone"
                    variant="outlined"
                    density="comfortable"
                    hide-details="auto"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    label="Address"
                    v-model="shippingInfo.address"
                    variant="outlined"
                    density="comfortable"
                    hide-details="auto"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    label="City"
                    v-model="shippingInfo.city"
                    variant="outlined"
                    density="comfortable"
                    hide-details="auto"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    label="Zip Code"
                    v-model="shippingInfo.zip"
                    variant="outlined"
                    density="comfortable"
                    hide-details="auto"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card>

            <v-card class="pa-6 rounded-lg" elevation="0" border>
              <h3 class="text-h6 font-weight-bold mb-4 d-flex align-center">
                <v-icon color="black" class="mr-2"
                  >mdi-credit-card-outline</v-icon
                >
                Payment Method
              </h3>

              <v-radio-group v-model="paymentMethod">
                <v-radio
                  label="Cash on Delivery (COD)"
                  value="cod"
                  color="black"
                ></v-radio>
                <v-radio
                  label="Credit Card"
                  value="card"
                  color="black"
                ></v-radio>
              </v-radio-group>

              <v-expand-transition>
                <div v-if="paymentMethod === 'card'">
                  <v-divider class="mb-4"></v-divider>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        label="Card Number"
                        placeholder="0000 0000 0000 0000"
                        prepend-inner-icon="mdi-credit-card"
                        variant="outlined"
                        density="comfortable"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        label="Expiry Date"
                        placeholder="MM/YY"
                        variant="outlined"
                        density="comfortable"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        label="CVV"
                        placeholder="123"
                        variant="outlined"
                        density="comfortable"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </div>
              </v-expand-transition>
            </v-card>
          </div>
        </v-col>

        <v-col cols="12" md="4">
          <v-card
            class="pa-5 rounded-lg position-sticky top-20"
            elevation="0"
            border
          >
            <h3 class="text-h6 font-weight-bold mb-4">Order Summary</h3>

            <div class="d-flex justify-space-between mb-2 text-body-2">
              <span class="text-grey-darken-1">Subtotal</span>
              <span class="font-weight-medium">${{ cartTotal }}</span>
            </div>

            <div class="d-flex justify-space-between mb-2 text-body-2">
              <span class="text-grey-darken-1">Shipping</span>
              <span class="font-weight-medium text-green">Free</span>
            </div>

            <div class="d-flex justify-space-between mb-2 text-body-2">
              <span class="text-grey-darken-1">Tax (14%)</span>
              <span class="font-weight-medium">${{ cartTax }}</span>
            </div>

            <v-divider class="my-4"></v-divider>

            <div class="d-flex justify-space-between mb-6 text-h6">
              <span class="font-weight-bold">Total</span>
              <span class="font-weight-bold">${{ finalTotal }}</span>
            </div>

            <v-btn
              block
              color="black"
              height="50"
              class="rounded-pill text-capitalize"
              elevation="2"
              @click="handleCheckoutAction"
              :loading="isLoading"
            >
              {{ isCheckout ? "Place Order" : "Proceed to Checkout" }}
            </v-btn>

            <div class="text-center mt-3">
              <v-icon
                size="small"
                icon="mdi-lock-outline"
                class="mr-1"
              ></v-icon>
              <span class="text-caption text-grey">Secure Checkout</span>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script setup>
import { computed, ref, reactive } from "vue";
import { AddInCart } from "@/store/Cart";

// 1. Store Setup
const cartStore = AddInCart();
const cartItems = computed(() => cartStore.CartItem || []);

// 2. State Variables ()
const isCheckout = ref(false); //     
const isLoading = ref(false); //  
const paymentMethod = ref("cod"); //   

const shippingInfo = reactive({
  name: "",
  phone: "",
  address: "",
  city: "",
  zip: "",
});

// 3. Cart Actions
const updateQuantity = (item, amount) => {
  cartStore.AddInCartItem({ id: item.id, quantity: amount });
};

const removeItem = (id) => {
  cartStore.removeFromCart(id);
};

// 4. Checkout Logic ( )
const handleCheckoutAction = () => {
  if (!isCheckout.value) {
    //     
    isCheckout.value = true;
    window.scrollTo({ top: 0, behavior: "smooth" });
  } else {
    //   
    placeOrder();
  }
};

const placeOrder = () => {
  //    ( )
  if (!shippingInfo.name || !shippingInfo.phone) {
    alert("Please fill in your shipping details.");
    return;
  }

  isLoading.value = true;

  //      Backend
  //   :
  setTimeout(() => {
    console.log("Order Placed Successfully!");
    console.log("Details:", {
      items: cartItems.value,
      shipping: shippingInfo,
      payment: paymentMethod.value,
      total: finalTotal.value,
    });

    isLoading.value = false;
    alert("Thank you! Your order has been placed.");

    //       
    // cartStore.clearCart();
    // router.push('/success');
  }, 2000);
};

// 5. Calculations
const cartTotal = computed(() => {
  return cartItems.value
    .reduce((total, item) => total + item.price * item.quantity, 0)
    .toFixed(2);
});

const cartTax = computed(() => {
  return (cartTotal.value * 0.14).toFixed(2);
});

const finalTotal = computed(() => {
  return (parseFloat(cartTotal.value) + parseFloat(cartTax.value)).toFixed(2);
});
</script>

<style scoped>
.qty-control {
  border: 1px solid #e0e0e0;
}
.position-sticky {
  position: sticky !important;
  top: 20px;
  z-index: 10;
}
</style>
