<script setup>
import { ref, computed } from "vue";

// ---    ---
const reviews = ref([
  {
    id: 1,
    userName: " ",
    productName: "iPhone 15 Pro Max",
    productImage: "https://cdn.dummyjson.com/product-images/1/thumbnail.jpg",
    rating: 5,
    comment:
      "             !",
    status: "",
    date: "2023-10-15",
  },
  {
    id: 2,
    userName: " ",
    productName: "MacBook Pro M3",
    productImage: "https://cdn.dummyjson.com/product-images/6/thumbnail.png",
    rating: 4,
    comment:
      "               .",
    status: " ",
    date: "2023-10-18",
  },
  {
    id: 3,
    userName: " ",
    productName: "Samsung S24 Ultra",
    productImage: "https://cdn.dummyjson.com/product-images/3/thumbnail.jpg",
    rating: 1,
    comment: "          !",
    status: "",
    date: "2023-10-20",
  },
  {
    id: 4,
    userName: " ",
    productName: "Sony Headphones",
    productImage: "https://cdn.dummyjson.com/product-images/4/thumbnail.jpg",
    rating: 5,
    comment: "       .",
    status: " ",
    date: "2023-10-22",
  },
]);

// ---    ---
const searchQuery = ref("");
const statusFilter = ref("");

// ---   (Dialog) ---
const viewDialog = ref(false);
const selectedReview = ref(null);

// ---  (Computed) ---
const pendingCount = computed(
  () => reviews.value.filter((r) => r.status === " ").length
);
const approvedCount = computed(
  () => reviews.value.filter((r) => r.status === "").length
);

// ---  (Functions) ---

//    
const getStatusColor = (status) => {
  if (status === "") return "success";
  if (status === "") return "error";
  return "warning"; //  
};

//  
const filteredReviews = computed(() => {
  return reviews.value.filter((review) => {
    const matchSearch =
      review.userName.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
      review.productName
        .toLowerCase()
        .includes(searchQuery.value.toLowerCase());

    const matchStatus =
      statusFilter.value === "" || review.status === statusFilter.value;

    return matchSearch && matchStatus;
  });
});

//   
const viewReview = (review) => {
  selectedReview.value = review;
  viewDialog.value = true;
};

//    ( / )
const changeStatus = (review, newStatus) => {
  const index = reviews.value.findIndex((r) => r.id === review.id);
  if (index !== -1) {
    reviews.value[index].status = newStatus;
  }
};

//  
const formatDate = (dateString) => {
  const options = { year: "numeric", month: "long", day: "numeric" };
  return new Date(dateString).toLocaleDateString("ar-EG", options);
};
</script>

