<template>
  <div class="favourateProduct pt-5">
    <v-row>
      <v-col cols="12">
        <h3 class="text-h5 font-weight-bold mb-5 text-grey-darken-3">
          {{
            isCheckout
              ? "Checkout & Payment"
              : `Product Favourites (${FavItem.length})`
          }}
        </h3>
      </v-col>
    </v-row>

    <v-row
      v-if="FavItem.length === 0"
      justify="center"
      class="text-justify py-10"
    >
      <v-col cols="12" md="6">
        <v-icon size="100" color="grey-lighten-1">mdi-heart-outline</v-icon>
        <h3 class="text-h5 text-grey mt-4">Your Favourate is empty</h3>
        <p class="text-body-1 text-grey mb-6">
          Looks like you haven't added anything yet.
        </p>
        <v-btn color="black" to="/" height="50" class="px-10 rounded-pill" flat>
          Continue Shopping
        </v-btn>
      </v-col>
    </v-row>
    <v-container>
      <v-row v-if="FavItem">
        <v-col
          cols="12"
          sm="6"
          md="4"
          lg="3"
          v-for="product in FavItem"
          :key="product.id"
        >
          <v-card class="product-card" width="250">
            <!-- Image + Quick View + Favourite Icon -->
            <div class="customBtnPopUp">
              <v-img :src="product.thumbnail" height="180" />
              <v-btn class="QuickVeiw" @click="QuickVeiw.open(product.id)">
                Quick View
              </v-btn>

              <!-- Heart Icon for Favourite -->
              <v-icon
                :color="isFavourite(product.id) ? 'red' : 'grey'"
                class="favourate"
                size="30"
                @click="toggleFavourite(product)"
              >
                mdi-heart
              </v-icon>
            </div>

            <div class="sale-badge" v-if="product.sale">
              {{ product.sale }}
            </div>
            <!-- Content -->
            <v-card-text>
              <h4 class="product-title text-justify">
                {{ shortTitle(product.title) }}
              </h4>
              <p class="product-desc text-justify py-3">
                {{ shortDescription(product.description) }}
              </p>

              <v-rating
                style="display: block; text-align: justify"
                class="py-2"
                size="20px"
                v-model="product.rating"
                readonly
                color="yellow-darken-2"
                half-increments
              ></v-rating>
              <v-card-text class="price text-justify px-0">
                <span style="font-weight: 600">Price: </span>
                <span>
                  <del> {{ product.price }} </del> From
                  {{
                    Math.ceil(
                      product.price -
                        product.price * (product.discountPercentage / 100)
                    )
                  }}</span
                >
              </v-card-text>
              <!-- <v-btn-toggle>
                <v-btn v-for="(pic, index) in product.images" :key="index"
                  >test</v-btn
                >
              </v-btn-toggle> -->
            </v-card-text>
            <!-- Actions -->
            <v-card-actions>
              <v-btn
                color="primary"
                variant="outlined"
                block
                @click="
                  $router.push({
                    name: 'ProductDetail',
                    params: { productId: product.id },
                  })
                "
              >
                Show Details
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
      <div class="text-center pt-10" v-else>
        <p class="font-weight-bold">Not Product Favourate</p>
      </div>
    </v-container>

    <PopUp />
  </div>
</template>

<script setup>
import { watch } from "vue";
import { AddFavProduct } from "../store/Favourate";
import PopUp from "../components/PopUp.vue";
import { usePopUpQuickView } from "@/store/PopUp";

const QuickVeiw = usePopUpQuickView();

//   store
const cartStore = AddFavProduct();
const FavItem = cartStore.FavItem;

//     undefined
const shortTitle = (text) =>
  text ? (text.length > 15 ? text.slice(0, 15) + "..." : text) : "";
const shortDescription = (text) =>
  text ? (text.length > 60 ? text.slice(0, 60) + "..." : text) : "";

//     (FavItem)
const toggleFavourite = (product) => {
  const exists = cartStore.FavItem.find((p) => p.id === product.id);
  if (exists) {
    cartStore.removeFromCart(product.id);
  } else {
    cartStore.AddFavProductItem({ ...product, quantity: 1 });
  }
};

//       
const isFavourite = (id) => cartStore.FavItem.some((p) => p.id === id);

//  FavItem   localStorage
watch(
  () => FavItem,
  (newVal) => {
    localStorage.setItem("cart-favourate", JSON.stringify(newVal));
  },
  { deep: true }
);
</script>

