<template>
  <div>
    <v-navigation-drawer
      v-model="drawer"
      location="right"
      width="300"
      temporary
      elevation="0"
      class="px-0"
    >
      <v-icon
        size="30"
        @click="drawer = false"
        style="position: absolute; top: 3px; left: 3px"
        >mdi-close</v-icon
      >
      <InCart />
      <v-container>
        <div>
          <v-btn
            color="primary"
            variant="outlined"
            block
            class="mt-4 px-3"
            height="50"
            @click="drawer = false"
            style="font-size: 12px; border-radius: 10px"
          >
            Proceed to Checkout
          </v-btn>
        </div>
        <div>
          <v-btn
            color="primary"
            variant=""
            block
            class="mt-4 px-3"
            height="50"
            border
            to="/User/CartPage"
            style="font-size: 12px; border-radius: 10px"
          >
            Show All Product
          </v-btn>
        </div>
      </v-container>
    </v-navigation-drawer>
  </div>
</template>
<script>
import InCart from "../components/InCart.vue";

export default {
  inject: ["emitter"],
  components: {
    InCart,
  },
  data() {
    return {
      drawer: false,
    };
  },
  mounted() {
    this.emitter.on("toggleCart", () => {
      this.drawer = !this.drawer;
    });
  },
};
</script>
