<template>
  <div class="flashdeal bg-grey-lighten-4 py-8">
    <v-container>
      <div class="d-flex align-center justify-center mb-10">
        <h2 class="text-h4 font-weight-black text-primary"> </h2>
      </div>

      <v-row
        v-if="!flashDeals.flashDeals || flashDeals.flashDeals.length === 0"
      >
        <v-col cols="12" sm="6" md="4" lg="3" v-for="num in 4" :key="num">
          <v-skeleton-loader
            type="image, article, button"
            elevation="2"
            class="rounded-xl"
          ></v-skeleton-loader>
        </v-col>
      </v-row>

      <v-slide-group v-else show-arrows class="custom-slider">
        <v-slide-group-item
          v-for="product in flashDeals.flashDeals"
          :key="product.id"
        >
          <v-card
            class="modern-card mx-3 my-4 rounded-xl d-flex flex-column"
            elevation="2"
          >
            <div class="img-container">
              <v-img
                :src="product.thumbnail"
                height="220"
                cover
                class="product-image"
              >
                <div
                  class="top-actions d-flex justify-space-between align-start w-100 pa-3"
                >
                  <v-chip
                    v-if="product.discountPercentage"
                    color="error"
                    size="small"
                    class="font-weight-bold elevation-1"
                  >
                    -{{ Math.round(product.discountPercentage) }}%
                  </v-chip>
                  <v-spacer v-else></v-spacer>

                  <v-btn
                    icon
                    size="small"
                    color="white"
                    variant="flat"
                    class="elevation-2 fav-btn"
                    @click.stop="toggleFavourite(product)"
                  >
                    <v-icon
                      size="20"
                      :color="
                        isFavourite(product.id) ? 'error' : 'grey-darken-2'
                      "
                    >
                      {{
                        isFavourite(product.id)
                          ? "mdi-heart"
                          : "mdi-heart-outline"
                      }}
                    </v-icon>
                  </v-btn>
                </div>

                <div
                  class="quick-view-overlay d-flex align-center justify-center"
                >
                  <v-btn
                    color="white"
                    rounded="pill"
                    class="text-primary font-weight-bold quick-btn"
                    @click.stop="QuickVeiw.open(product.id)"
                  >
                    <v-icon left size="small" class="mr-1">mdi-eye</v-icon>
                    Quick View
                  </v-btn>
                </div>
              </v-img>
            </div>

            <v-card-text class="flex-grow-1 pt-4 pb-2">
              <div class="d-flex justify-center align-center mb-1">
                <v-rating
                  :model-value="product.rating"
                  color="amber-darken-2"
                  density="compact"
                  half-increments
                  readonly
                  size="small"
                ></v-rating>
                <span class="text-caption text-grey-darken-1 ml-2"
                  >({{ product.rating }})</span
                >
              </div>

              <h3
                class="text-subtitle-1 font-weight-bold mb-1"
                :title="product.title"
              >
                {{ shortTitle(product.title) }}
              </h3>

              <p
                class="text-caption text-grey-darken-1 mb-2 desc-text"
                :title="product.description"
              >
                {{ shortDescription(product.description) }}
              </p>

              <div class="d-flex align-center justify-center flex-wrap gap-2">
                <span class="text-h6 font-weight-black text-primary">
                  {{
                    Math.ceil(
                      product.price -
                        product.price * (product.discountPercentage / 100)
                    )
                  }}
                  .
                </span>
                <span
                  class="text-decoration-line-through text-grey text-caption mb-1"
                >
                  {{ product.price }}
                </span>
              </div>
              <div class="d-flex align-center my-3 justify-center">
                <span class="text-caption font-weight-bold text-success">
                    
                  <span style="color: #000">: {{ product.stock }}</span>
                </span>
              </div>
            </v-card-text>

            <v-card-actions class="px-4 pb-4 pt-0">
              <v-btn
                color="primary"
                variant="flat"
                block
                rounded="lg"
                class="text-none font-weight-bold shadow-hover"
                @click="
                  $router.push({
                    name: 'ProductDetail',
                    params: { productId: product.id },
                  })
                "
              >
                Show Details
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-slide-group-item>
      </v-slide-group>
    </v-container>
  </div>
  <PopUp />
</template>

<script setup>
import { ProductModule } from "@/store/Products.js";
import { VSkeletonLoader } from "vuetify/lib/components";
import PopUp from "./PopUp.vue";
import { usePopUpQuickView } from "@/store/PopUp";
import { onMounted, watch } from "vue";
import { AddFavProduct } from "../store/Favourate";

const QuickVeiw = usePopUpQuickView();
const flashDeals = ProductModule();

const shortDescription = (text) => {
  if (!text) return "";
  return text.length > 60 ? text.substring(0, 50) + "..." : text;
};

const shortTitle = (text) => {
  if (!text) return "";
  return text.length > 15 ? text.substring(0, 15) + "..." : text;
};

const cartStore = AddFavProduct();
const FavItem = cartStore.FavItem;

const toggleFavourite = (product) => {
  const exists = cartStore.FavItem.find((p) => p.id === product.id);
  if (exists) {
    cartStore.removeFromCart(product.id);
  } else {
    cartStore.AddFavProductItem({ ...product, quantity: 1 });
  }
};

const isFavourite = (id) => cartStore.FavItem.some((p) => p.id === id);

watch(
  () => FavItem,
  (newVal) => {
    localStorage.setItem("cart-favourate", JSON.stringify(newVal));
  },
  { deep: true }
);

onMounted(async () => {
  await flashDeals.getProducts();
});
</script>

