<template>
  <v-locale-provider rtl>
    <v-container fluid class="pa-6" dir="rtl">
      <v-card class="mb-6 rounded-xl" elevation="1">
        <v-card-text
          class="d-flex align-center justify-space-between flex-wrap gap-4 pa-4"
        >
          <h2 class="text-h5 font-weight-black" style="color: rgb(14, 22, 67)">
             
          </h2>

          <div
            class="d-flex align-center gap-3 flex-grow-1"
            style="max-width: 400px"
          >
            <v-text-field
              v-model="searchQuery"
              prepend-inner-icon="mdi-magnify"
              label="    ..."
              variant="outlined"
              density="compact"
              hide-details
              class="bg-white rounded-lg"
            ></v-text-field>
          </div>
        </v-card-text>
      </v-card>

      <v-card class="rounded-xl overflow-hidden" elevation="1">
        <v-table class="users-table pa-2">
          <thead class="bg-grey-lighten-4">
            <tr>
              <th class="text-right font-weight-bold"></th>
              <th class="text-right font-weight-bold"> </th>
              <th class="text-right font-weight-bold"> </th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredUsers.length === 0">
              <td colspan="6" class="text-center pa-6 text-grey-darken-1">
                    .
              </td>
            </tr>

            <tr v-for="user in filteredUsers" :key="user.id" class="hover-row">
              <td class="d-flex align-center gap-3 py-3">
                <v-avatar
                  size="45"
                  class="bg-primary text-white font-weight-bold"
                >
                  {{ user.name.charAt(0) }}
                </v-avatar>
                <span class="font-weight-bold text-subtitle-2">{{
                  user.name
                }}</span>
              </td>
              <td class="text-grey-darken-1">{{ user.email }}</td>
              <td
                class="text-grey-darken-1"
                dir="ltr"
                style="text-align: right"
              >
                {{ user.phone }}
              </td>
              <td class="text-center font-weight-bold text-primary">
                {{ user.ordersCount }}
              </td>
              <td class="text-center">
                <v-chip
                  size="small"
                  :color="user.isActive ? 'success' : 'error'"
                  class="font-weight-bold"
                >
                  {{ user.isActive ? "" : "" }}
                </v-chip>
              </td>
              <td class="text-center">
                <v-tooltip location="top">
                  <template v-slot:activator="{ props }">
                    <v-btn
                      icon
                      size="small"
                      :color="user.isActive ? 'error' : 'success'"
                      variant="text"
                      v-bind="props"
                      @click="toggleUserStatus(user)"
                    >
                      <v-icon>{{
                        user.isActive
                          ? "mdi-block-helper"
                          : "mdi-check-circle-outline"
                      }}</v-icon>
                    </v-btn>
                  </template>
                  <span>{{
                    user.isActive ? " " : " "
                  }}</span>
                </v-tooltip>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref, computed } from "vue";

//   
const users = ref([
  {
    id: 1,
    name: " ",
    email: "ahmed@example.com",
    phone: "01012345678",
    ordersCount: 12,
    isActive: true,
  },
  {
    id: 2,
    name: " ",
    email: "sara@example.com",
    phone: "01198765432",
    ordersCount: 5,
    isActive: true,
  },
  {
    id: 3,
    name: " ",
    email: "mohamed@example.com",
    phone: "01234567890",
    ordersCount: 20,
    isActive: false,
  },
  {
    id: 4,
    name: " ",
    email: "fatima@example.com",
    phone: "01555555555",
    ordersCount: 1,
    isActive: true,
  },
]);

const searchQuery = ref("");

//  
const filteredUsers = computed(() => {
  if (!searchQuery.value) return users.value;
  const lowerCaseQuery = searchQuery.value.toLowerCase();
  return users.value.filter(
    (user) =>
      user.name.toLowerCase().includes(lowerCaseQuery) ||
      user.email.toLowerCase().includes(lowerCaseQuery)
  );
});

//    ( / )
const toggleUserStatus = (user) => {
  const index = users.value.findIndex((u) => u.id === user.id);
  if (index !== -1) {
    users.value[index].isActive = !users.value[index].isActive;
  }
};
</script>

<style scoped>
.users-table th {
  color: rgb(14, 22, 67) !important;
  font-size: 15px !important;
  border-bottom: 2px solid #eee !important;
  white-space: nowrap;
}

.users-table td {
  border-bottom: 1px solid #f5f5f5 !important;
  vertical-align: middle !important;
}

.hover-row {
  transition: background-color 0.2s ease;
}

.hover-row:hover {
  background-color: #f8f9fa;
}

.gap-3 {
  gap: 12px;
}
.gap-4 {
  gap: 16px;
}
</style>
