<template>
  <v-locale-provider rtl>
    <v-container fluid class="pa-6" dir="rtl">
      <v-card class="mb-6 rounded-xl" elevation="1">
        <v-card-text
          class="d-flex align-center justify-space-between flex-wrap gap-4 pa-4"
        >
          <div class="d-flex align-center gap-3">
            <v-avatar color="rgb(14, 22, 67)" size="48" class="rounded-lg">
              <v-icon color="white" size="28">mdi-warehouse</v-icon>
            </v-avatar>
            <h2
              class="text-h5 font-weight-black"
              style="color: rgb(14, 22, 67)"
            >
               
            </h2>
          </div>

          <div
            class="d-flex align-center gap-3 flex-grow-1 flex-wrap"
            style="max-width: 650px"
          >
            <v-text-field
              v-model="searchQuery"
              prepend-inner-icon="mdi-magnify"
              label="     (SKU)..."
              variant="outlined"
              density="compact"
              hide-details
              class="bg-white rounded-lg flex-grow-1"
            ></v-text-field>

            <v-select
              v-model="statusFilter"
              :items="statusOptions"
              label=" "
              variant="outlined"
              density="compact"
              hide-details
              clearable
              class="bg-white rounded-lg"
              style="min-width: 150px; max-width: 200px"
            ></v-select>

            <v-btn
              color="success"
              variant="tonal"
              prepend-icon="mdi-file-excel"
              class="font-weight-bold rounded-lg"
              height="44"
            >
               
            </v-btn>
          </div>
        </v-card-text>
      </v-card>

      <v-row class="mb-4">
        <v-col cols="12" md="4">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-primary"
            elevation="1"
          >
            <v-avatar
              color="primary-lighten-4"
              size="50"
              class="mr-3 text-primary"
            >
              <v-icon>mdi-package-variant</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                 
              </div>
              <div class="text-h5 font-weight-black">
                {{ inventory.length }}
              </div>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="4">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-warning"
            elevation="1"
          >
            <v-avatar
              color="orange-lighten-4"
              size="50"
              class="mr-3 text-warning"
            >
              <v-icon>mdi-alert-outline</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                   
              </div>
              <div class="text-h5 font-weight-black text-warning">
                {{ lowStockCount }}
              </div>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="4">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-error"
            elevation="1"
          >
            <v-avatar color="red-lighten-4" size="50" class="mr-3 text-error">
              <v-icon>mdi-close-circle-outline</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                  (Out of Stock)
              </div>
              <div class="text-h5 font-weight-black text-error">
                {{ outOfStockCount }}
              </div>
            </div>
          </v-card>
        </v-col>
      </v-row>

      <v-card class="rounded-xl overflow-hidden" elevation="1">
        <v-table class="inventory-table pa-2">
          <thead class="bg-grey-lighten-4">
            <tr>
              <th class="text-right font-weight-bold"></th>
              <th class="text-right font-weight-bold"> (SKU)</th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"></th>
              <th class="text-center font-weight-bold"> </th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredInventory.length === 0">
              <td colspan="6" class="text-center pa-6 text-grey-darken-1">
                    .
              </td>
            </tr>

            <tr
              v-for="item in filteredInventory"
              :key="item.id"
              class="hover-row"
            >
              <td class="d-flex align-center gap-3 py-2">
                <v-avatar size="40" rounded class="bg-grey-lighten-2">
                  <v-img :src="item.image" cover></v-img>
                </v-avatar>
                <span class="font-weight-bold text-subtitle-2">{{
                  item.name
                }}</span>
              </td>
              <td class="text-grey-darken-1 font-weight-medium">
                {{ item.sku }}
              </td>

              <td class="text-center">
                <span
                  class="text-h6 font-weight-black"
                  :class="getQuantityColorText(item)"
                >
                  {{ item.quantity }}
                </span>
              </td>

              <td class="text-center text-grey-darken-1">
                {{ item.minQuantity }}
              </td>

              <td class="text-center">
                <v-chip
                  size="small"
                  :color="getStatus(item).color"
                  class="font-weight-bold"
                >
                  <v-icon left size="16" class="mr-1">{{
                    getStatus(item).icon
                  }}</v-icon>
                  {{ getStatus(item).text }}
                </v-chip>
              </td>

              <td class="text-center">
                <v-btn
                  color="primary"
                  variant="tonal"
                  size="small"
                  class="font-weight-bold rounded-lg px-4"
                  @click="openAdjustDialog(item)"
                >
                  
                  <v-icon right size="18" class="ml-1"
                    >mdi-swap-vertical</v-icon
                  >
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>

      <v-dialog v-model="adjustDialog" max-width="450px" persistent>
        <v-card class="rounded-xl pa-2" v-if="selectedItem">
          <v-card-title
            class="text-h6 font-weight-bold pt-4 px-4"
            style="color: rgb(14, 22, 67)"
          >
              
          </v-card-title>
          <v-card-subtitle class="px-4 pb-2 text-primary font-weight-bold">
            {{ selectedItem.name }} ({{ selectedItem.sku }})
          </v-card-subtitle>
          <v-divider class="my-2"></v-divider>

          <v-card-text>
            <div
              class="d-flex justify-space-between align-center mb-6 pa-3 bg-grey-lighten-4 rounded-lg"
            >
              <span class="font-weight-bold">   :</span>
              <span class="text-h6 font-weight-black">{{
                selectedItem.quantity
              }}</span>
            </div>

            <v-form ref="form" v-model="validForm">
              <v-radio-group v-model="adjustmentType" inline class="mb-2">
                <v-radio
                  label=" ( )"
                  value="add"
                  color="success"
                ></v-radio>
                <v-radio
                  label=" ( / )"
                  value="deduct"
                  color="error"
                ></v-radio>
              </v-radio-group>

              <v-text-field
                v-model.number="adjustmentAmount"
                label=""
                type="number"
                variant="outlined"
                color="primary"
                :rules="[
                  (v) => !!v || '  ',
                  (v) => v > 0 || '      ',
                  (v) =>
                    (adjustmentType === 'deduct'
                      ? v <= selectedItem.quantity
                      : true) || '    ',
                ]"
              >
                <template v-slot:prepend-inner>
                  <v-icon
                    :color="adjustmentType === 'add' ? 'success' : 'error'"
                  >
                    {{
                      adjustmentType === "add"
                        ? "mdi-plus-circle"
                        : "mdi-minus-circle"
                    }}
                  </v-icon>
                </template>
              </v-text-field>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-4">
            <v-spacer></v-spacer>
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-6 font-weight-bold"
              @click="closeAdjustDialog"
              ></v-btn
            >
            <v-btn
              :color="adjustmentType === 'add' ? 'success' : 'error'"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              :disabled="!validForm"
              @click="saveStockAdjustment"
            >
               
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref, computed } from "vue";

// ---    ---
// minQuantity:           ()
const inventory = ref([
  {
    id: 1,
    name: "iPhone 15 Pro Max",
    sku: "APL-IP15PM-256",
    quantity: 45,
    minQuantity: 10,
    image: "https://cdn.dummyjson.com/product-images/1/thumbnail.jpg",
  },
  {
    id: 2,
    name: "Samsung Galaxy S24 Ultra",
    sku: "SAM-S24U-512",
    quantity: 8,
    minQuantity: 15,
    image: "https://cdn.dummyjson.com/product-images/3/thumbnail.jpg",
  },
  {
    id: 3,
    name: "MacBook Pro M3",
    sku: "APL-MBP-M3-14",
    quantity: 0,
    minQuantity: 5,
    image: "https://cdn.dummyjson.com/product-images/6/thumbnail.png",
  },
  {
    id: 4,
    name: "Sony WH-1000XM5",
    sku: "SNY-WH1000-B",
    quantity: 12,
    minQuantity: 10,
    image: "https://cdn.dummyjson.com/product-images/4/thumbnail.jpg",
  },
  {
    id: 5,
    name: "Dell XPS 15",
    sku: "DEL-XPS15-9530",
    quantity: 3,
    minQuantity: 5,
    image: "https://cdn.dummyjson.com/product-images/7/thumbnail.jpg",
  },
]);

// ---    ---
const searchQuery = ref("");
const statusFilter = ref(null);
const statusOptions = ["", "  ", " "];

// ---    ---
const adjustDialog = ref(false);
const selectedItem = ref(null);
const validForm = ref(false);
const adjustmentType = ref("add"); // 'add'  'deduct'
const adjustmentAmount = ref(null);

// ---   ---
const lowStockCount = computed(
  () =>
    inventory.value.filter((i) => i.quantity > 0 && i.quantity <= i.minQuantity)
      .length
);
const outOfStockCount = computed(
  () => inventory.value.filter((i) => i.quantity === 0).length
);

// ---    ---

//    (    )   
const getStatus = (item) => {
  if (item.quantity === 0) {
    return { text: " ", color: "error", icon: "mdi-close-circle" };
  } else if (item.quantity <= item.minQuantity) {
    return { text: "  ", color: "warning", icon: "mdi-alert" };
  } else {
    return { text: "", color: "success", icon: "mdi-check-circle" };
  }
};

//   
const getQuantityColorText = (item) => {
  if (item.quantity === 0) return "text-error";
  if (item.quantity <= item.minQuantity) return "text-warning";
  return "text-success";
};

// ---   ---
const filteredInventory = computed(() => {
  return inventory.value.filter((item) => {
    // 1.   (   SKU)
    const query = searchQuery.value.toLowerCase();
    const matchesSearch =
      item.name.toLowerCase().includes(query) ||
      item.sku.toLowerCase().includes(query);

    // 2.  
    let matchesStatus = true;
    if (statusFilter.value) {
      const statusText = getStatus(item).text;
      matchesStatus = statusText === statusFilter.value;
    }

    return matchesSearch && matchesStatus;
  });
});

// ---    (Dialog) ---
const openAdjustDialog = (item) => {
  selectedItem.value = { ...item }; //    
  adjustmentType.value = "add";
  adjustmentAmount.value = null;
  adjustDialog.value = true;
};

const closeAdjustDialog = () => {
  adjustDialog.value = false;
  setTimeout(() => {
    selectedItem.value = null;
    adjustmentAmount.value = null;
  }, 300);
};

const saveStockAdjustment = () => {
  //       
  const index = inventory.value.findIndex(
    (i) => i.id === selectedItem.value.id
  );
  if (index !== -1) {
    const amount = Number(adjustmentAmount.value);
    if (adjustmentType.value === "add") {
      inventory.value[index].quantity += amount;
    } else if (adjustmentType.value === "deduct") {
      inventory.value[index].quantity -= amount;
    }
  }
  closeAdjustDialog();
};
</script>

