import { defineStore } from "pinia";

export const AddFavProduct = defineStore("AddFavProduct", {
  state: () => ({
    FavItem: JSON.parse(localStorage.getItem("cart-favourate")) || [],
  }),

  actions: {
    AddFavProductItem(item) {
      const existingItem = this.FavItem.find(
        (product) => product.id == item.id
      );
      if (existingItem) {
        existingItem.quantity += item.quantity;
      } else {
        this.FavItem.push(item);
      }
      localStorage.setItem("cart-favourate", JSON.stringify(this.FavItem));
      console.log(this.FavItem);
    },
    removeFromCart(id) {
      const index = this.FavItem.findIndex((item) => item.id === id);

      if (index !== -1) {
        this.FavItem.splice(index, 1);
      }

      localStorage.setItem("cart-favourate", JSON.stringify(this.FavItem));
    },
  },
});
