<template>
  <v-locale-provider rtl>
    <v-container fluid class="pa-6" dir="rtl">
      <v-card class="mb-6 rounded-xl" elevation="1">
        <v-card-text
          class="d-flex align-center justify-space-between flex-wrap gap-4 pa-4"
        >
          <h2 class="text-h5 font-weight-black" style="color: rgb(14, 22, 67)">
             
          </h2>

          <div
            class="d-flex align-center gap-3 flex-grow-1"
            style="max-width: 500px"
          >
            <v-text-field
              v-model="searchQuery"
              prepend-inner-icon="mdi-magnify"
              label="  ..."
              variant="outlined"
              density="compact"
              hide-details
              class="bg-white rounded-lg"
            ></v-text-field>

            <v-btn
              color="primary"
              prepend-icon="mdi-plus"
              class="font-weight-bold px-6 rounded-lg shadow-btn"
              height="44"
              @click="openAddDialog"
            >
               
            </v-btn>
          </div>
        </v-card-text>
      </v-card>

      <v-card class="rounded-xl overflow-hidden" elevation="1">
        <v-table class="products-table pa-2">
          <thead class="bg-grey-lighten-4">
            <tr>
              <th class="text-right font-weight-bold"></th>
              <th class="text-right font-weight-bold"> </th>
              <th class="text-right font-weight-bold"></th>
              <th class="text-right font-weight-bold"></th>
              <th class="text-right font-weight-bold"></th>
              <th class="text-right font-weight-bold"></th>
              <th class="text-center font-weight-bold"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredProducts.length === 0">
              <td colspan="7" class="text-center pa-6 text-grey-darken-1">
                    .
              </td>
            </tr>

            <tr
              v-for="item in filteredProducts"
              :key="item.id"
              class="hover-row"
            >
              <td>
                <v-avatar
                  size="50"
                  rounded="lg"
                  class="elevation-1 my-2 bg-grey-lighten-3"
                >
                  <v-img :src="item.thumbnail" cover></v-img>
                </v-avatar>
              </td>
              <td class="font-weight-bold text-subtitle-2">
                {{ shortTitle(item.title) }}
              </td>
              <td>
                <v-chip size="small" color="info" variant="tonal">{{
                  item.category
                }}</v-chip>
              </td>
              <td class="font-weight-bold text-success">${{ item.price }}</td>
              <td>
                <v-chip
                  size="small"
                  color="error"
                  v-if="item.discountPercentage"
                >
                  -{{ Math.round(item.discountPercentage) }}%
                </v-chip>
                <span v-else class="text-grey">-</span>
              </td>
              <td>
                <v-chip
                  size="small"
                  :color="item.stock > 10 ? 'success' : 'error'"
                >
                  {{ item.stock }}
                </v-chip>
              </td>
              <td class="text-center">
                <v-btn
                  icon
                  size="small"
                  color="primary"
                  variant="text"
                  @click="editItem(item)"
                >
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                  icon
                  size="small"
                  color="error"
                  variant="text"
                  @click="openDeleteDialog(item)"
                >
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>

      <v-dialog v-model="dialog" max-width="600px" persistent>
        <v-card class="rounded-xl pa-2">
          <v-card-title
            class="text-h5 font-weight-bold pt-4 px-4"
            style="color: rgb(14, 22, 67)"
          >
            {{ isEditing ? " " : "  " }}
          </v-card-title>
          <v-divider class="my-3"></v-divider>

          <v-card-text>
            <v-form ref="form" v-model="validForm">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.title"
                    label=" "
                    variant="outlined"
                    color="primary"
                    :rules="[(v) => !!v || ' ']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="editedItem.category"
                    label=""
                    variant="outlined"
                    color="primary"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.number="editedItem.price"
                    label=" ($)"
                    type="number"
                    variant="outlined"
                    color="primary"
                    :rules="[(v) => !!v || ' ']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.number="editedItem.discountPercentage"
                    label="  (%)"
                    type="number"
                    variant="outlined"
                    color="primary"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.number="editedItem.stock"
                    label=" ()"
                    type="number"
                    variant="outlined"
                    color="primary"
                  ></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-file-input
                    v-model="imageFile"
                    label="    "
                    variant="outlined"
                    color="primary"
                    accept="image/*"
                    prepend-icon=""
                    prepend-inner-icon="mdi-camera"
                    @update:modelValue="handleImageUpload"
                    :rules="[
                      (v) => isEditing || !!previewImage || ' ',
                    ]"
                  ></v-file-input>
                </v-col>

                <v-col
                  cols="12"
                  class="d-flex justify-center"
                  v-if="previewImage"
                >
                  <v-card elevation="2" class="rounded-lg pa-1">
                    <v-img
                      :src="previewImage"
                      width="120"
                      height="120"
                      cover
                      class="rounded"
                    ></v-img>
                  </v-card>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-4">
            <v-spacer></v-spacer>
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-4 font-weight-bold"
              @click="closeDialog"
              ></v-btn
            >
            <v-btn
              color="primary"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              :disabled="!validForm"
              @click="saveItem"
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="400px">
        <v-card class="rounded-xl pa-4 text-center">
          <v-icon color="error" size="60" class="mb-3"
            >mdi-alert-circle-outline</v-icon
          >
          <v-card-title class="text-h6 font-weight-bold text-wrap"
            >      </v-card-title
          >
          <v-card-actions class="d-flex justify-center mt-4">
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-6 font-weight-bold"
              @click="dialogDelete = false"
              ></v-btn
            >
            <v-btn
              color="error"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              @click="confirmDelete"
              > </v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref, computed } from "vue";

//  
const products = ref([
  {
    id: 1,
    title: "iPhone 15 Pro Max",
    category: "smartphones",
    price: 1200,
    discountPercentage: 10,
    stock: 25,
    thumbnail: "https://cdn.dummyjson.com/product-images/1/thumbnail.jpg",
  },
  {
    id: 2,
    title: "MacBook Pro M3",
    category: "laptops",
    price: 2500,
    discountPercentage: 5,
    stock: 8,
    thumbnail: "https://cdn.dummyjson.com/product-images/2/thumbnail.jpg",
  },
  {
    id: 3,
    title: "Samsung Galaxy S24 Ultra",
    category: "smartphones",
    price: 1100,
    discountPercentage: 15,
    stock: 0,
    thumbnail: "https://cdn.dummyjson.com/product-images/3/thumbnail.jpg",
  },
]);

//  
const searchQuery = ref("");
const dialog = ref(false);
const dialogDelete = ref(false);
const isEditing = ref(false);
const validForm = ref(false);
const itemToDelete = ref(null);

//   
const imageFile = ref(null);
const previewImage = ref("");

//   
const defaultItem = {
  id: null,
  title: "",
  category: "",
  price: 0,
  discountPercentage: 0,
  stock: 0,
  thumbnail: "",
};

const editedItem = ref({ ...defaultItem });

//      
const handleImageUpload = (fileArray) => {
  //     3  Array   
  const file = Array.isArray(fileArray) ? fileArray[0] : fileArray;

  if (file) {
    //       
    previewImage.value = URL.createObjectURL(file);
    editedItem.value.thumbnail = previewImage.value;
  } else {
    //     
    if (!isEditing.value) {
      previewImage.value = "";
      editedItem.value.thumbnail = "";
    }
  }
};

const shortTitle = (text) => {
  if (!text) return "";
  return text.length > 25 ? text.substring(0, 25) + "..." : text;
};

const filteredProducts = computed(() => {
  if (!searchQuery.value) return products.value;
  const lowerCaseQuery = searchQuery.value.toLowerCase();
  return products.value.filter(
    (product) =>
      product.title.toLowerCase().includes(lowerCaseQuery) ||
      product.category.toLowerCase().includes(lowerCaseQuery)
  );
});

const openAddDialog = () => {
  isEditing.value = false;
  editedItem.value = { ...defaultItem };
  imageFile.value = null; //   
  previewImage.value = ""; //  
  dialog.value = true;
};

const editItem = (item) => {
  isEditing.value = true;
  editedItem.value = { ...item };
  imageFile.value = null;
  previewImage.value = item.thumbnail; //   
  dialog.value = true;
};

const closeDialog = () => {
  dialog.value = false;
  setTimeout(() => {
    editedItem.value = { ...defaultItem };
    imageFile.value = null;
    previewImage.value = "";
  }, 300);
};

const saveItem = () => {
  if (isEditing.value) {
    const index = products.value.findIndex((p) => p.id === editedItem.value.id);
    if (index !== -1) {
      products.value[index] = { ...editedItem.value };
    }
  } else {
    editedItem.value.id = Date.now();
    products.value.unshift({ ...editedItem.value });
  }
  closeDialog();
};

const openDeleteDialog = (item) => {
  itemToDelete.value = item;
  dialogDelete.value = true;
};

const confirmDelete = () => {
  products.value = products.value.filter((p) => p.id !== itemToDelete.value.id);
  dialogDelete.value = false;
  itemToDelete.value = null;
};
</script>

<style scoped>
.shadow-btn {
  box-shadow: 0 4px 12px rgba(24, 103, 192, 0.3) !important;
  transition: transform 0.2s ease;
}
.shadow-btn:hover {
  transform: translateY(-2px);
}

.products-table th {
  color: rgb(14, 22, 67) !important;
  font-size: 15px !important;
  border-bottom: 2px solid #eee !important;
  white-space: nowrap;
}

.products-table td {
  border-bottom: 1px solid #f5f5f5 !important;
  vertical-align: middle !important;
}

.hover-row {
  transition: background-color 0.2s ease;
}

.hover-row:hover {
  background-color: #f8f9fa;
}

.gap-3 {
  gap: 12px;
}
.gap-4 {
  gap: 16px;
}
</style>
