<template>
  <div class="myabout">
    <h1>this is about page</h1>
    <h2>show ToDo</h2>
    <table class="todo-table">
      <thead>
        <tr>
          <th>#</th>
          <th></th>
          <th> </th>
          <th> </th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody id="todo-body">
        <tr
          v-for="(item, index) in todoList"
          :key="item.id"
          :style="`${
            item.isCompleted ? 'background: red' : 'background: white'
          }`"
        >
          <td>{{ index }}</td>
          <td>{{ item.text }}</td>
          <td>{{ item.from }}</td>
          <td>{{ item.to }}</td>
          <td>{{ item.createdAt }}</td>
          <td class="options">
            <button @click="iscomplete(item)" class="btn-complete">
              Completed
            </button>
            <button @click="deletetodo(index)" class="btn-delete">
              Delete
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script setup>
import { ref, onMounted } from "vue";
const todoList = ref([]);
const deletetodo = (index) => {
  todoList.value.splice(index, 1);
  addlocaltodo();
};

const addlocaltodo = () => {
  localStorage.setItem("toDo", JSON.stringify(todoList.value));
};
const iscomplete = (item) => {
  item.isCompleted = !item.isCompleted;
  addlocaltodo();
};
const updateToDo = () => {
  const savedData = localStorage.getItem("toDo");
  if (savedData) {
    todoList.value = JSON.parse(savedData);
  }
};
onMounted(() => {
  updateToDo();
});
</script>
