<template>
  <div class="pt-5">
    <v-container>
      <v-row v-if="productDetails.productDetails?.id">
        <v-col cols="7">
          <img
            :src="tab ? tab : productDetails.productDetails.thumbnail"
            class="w-100 border"
            height="400px"
            alt=""
          />
          <v-tabs center-active v-model="tab" height="250px">
            <v-tab
              v-for="(img, i) in productDetails.productDetails.images"
              :key="i"
              class="mx-10"
              :value="img"
            >
              <img :src="img" alt="not found" width="" height="220" />
            </v-tab>
          </v-tabs>
        </v-col>
        <v-col cols="5">
          <v-card class="text-justify">
            <v-card-title>{{
              productDetails.productDetails.title
            }}</v-card-title>
            <v-card-text>{{
              productDetails.productDetails.description
            }}</v-card-text>
            <v-rating
              v-model="productDetails.productDetails.rating"
              readonly
              color="yellow-darken-2"
              half-increments
            ></v-rating>
            <v-card-text>
              stock:
              {{
                productDetails.productDetails.stock > 0
                  ? productDetails.productDetails.stock
                  : "No Stock"
              }}
            </v-card-text>
            <v-card-text>
              Sale: -{{
                Math.round(productDetails.productDetails.discountPercentage)
              }}%
            </v-card-text>
            <v-card-text>
              Brand:
              {{
                productDetails.productDetails.brand
                  ? productDetails.productDetails.brand
                  : "No brand"
              }}
            </v-card-text>
            <v-card-text>
              <del>Price: {{ productDetails.productDetails.price }}</del> From
              {{
                Math.ceil(
                  productDetails.productDetails.price -
                    productDetails.productDetails.price *
                      (productDetails.productDetails.discountPercentage / 100)
                )
              }}
            </v-card-text>
            <div class="py-4">
              <v-icon size="22" @click="decrease">mdi-minus</v-icon>
              <input type="number" value="1" v-model="quantity" />
              <v-icon size="22" @click="increase">mdi-plus</v-icon>
            </div>
            <v-card-actions>
              <v-btn
                color="white"
                style="
                  background-color: black;
                  border-radius: 30px;
                  height: 50px;
                "
                :loading="loading"
                :disabled="loading"
                @click="AddToCart(productDetails.productDetails)"
                variant="outlined"
                block
              >
                Add to cart
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>

      <!-- Loader -->
      <v-skeleton-loader v-else type="image, card" />
    </v-container>
  </div>
</template>

<script setup>
import { ProductModule } from "@/store/Products";
import { onMounted, ref, watch } from "vue";
import { useRoute } from "vue-router";
import { AddInCart } from "../../store/Cart";
const CartItem = AddInCart();
import { inject } from "vue";
const emitter = inject("emitter");
import { usePopUpQuickView } from "@/store/PopUp";
const loading = ref(false);

const QuickVeiw = usePopUpQuickView();

const route = useRoute();
const productDetails = ProductModule();
const tab = ref(null);
const quantity = ref(1);
const increase = () => quantity.value++;
const decrease = () => {
  if (quantity.value > 1) quantity.value--;
};
const loadProductData = async () => {
  tab.value = null;
  productDetails.productDetails = {};
  quantity.value = 1;
  await productDetails.getSingleProducts(route.params.productId);
};
const AddToCart = (item) => {
  loading.value = true;

  const product = { ...item, quantity: quantity.value };

  setTimeout(() => {
    CartItem.AddInCartItem(product);
    loading.value = false;
    QuickVeiw.close();
    emitter.emit("toggleCart");
    emitter.emit("showMessage", product.title);
  }, 1000); // 1 second
};
onMounted(async () => {
  await loadProductData();
});

watch(
  () => route.params.productId,
  async (newId) => {
    if (newId) {
      await loadProductData();
    }
  }
);
</script>

