            >
              
              {{ activeTab === "sliders" ? " (Slide)" : " " }}
            </v-btn>
          </div>

          <v-tabs v-model="activeTab" color="primary" class="border-bottom">
            <v-tab value="sliders" class="font-weight-bold text-subtitle-1">
              <v-icon right class="ml-2">mdi-layers-outline</v-icon>
               
            </v-tab>
            <v-tab value="banners" class="font-weight-bold text-subtitle-1">
              <v-icon right class="ml-2">mdi-image-area</v-icon>
               
            </v-tab>
          </v-tabs>
        </v-card-text>
      </v-card>

      <v-window v-model="activeTab">
        <v-window-item value="sliders">
          <v-row>
            <v-col
              cols="12"
