                    variant="text"
                    @click="editItem(slide)"
                  >
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                  <v-btn
                    icon
                    size="small"
                    color="error"
                    variant="text"
                    @click="openDeleteDialog(slide)"
                  >
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-window-item>

        <v-window-item value="banners">
          <v-row>
            <v-col
              cols="12"
              class="text-center py-10"
              v-if="banners.length === 0"
            >
              <v-icon size="60" color="grey-lighten-1"
                >mdi-image-off-outline</v-icon
              >
              <p class="text-grey-darken-1 mt-2 font-weight-bold">
                    .
              </p>
            </v-col>

            <v-col cols="12" md="6" v-for="banner in banners" :key="banner.id">
              <v-card
                class="rounded-xl overflow-hidden banner-card"
                elevation="2"
              >
                <v-img :src="banner.image" height="150" cover>
                  <div class="d-flex justify-space-between pa-2">
                    <v-chip
                      size="small"
                      color="rgb(14, 22, 67)"
                      class="font-weight-bold text-white"
                    >
                      {{
                        banner.position === "top"
                          ? " "
                          : banner.position === "middle"
                          ? " "
                          : " "
                      }}
                    </v-chip>
                    <v-chip
                      size="small"
                      :color="banner.isActive ? 'success' : 'error'"
                      class="font-weight-bold"
                    >
                      {{ banner.isActive ? "" : "" }}
                    </v-chip>
                  </div>
                </v-img>

                <v-card-actions class="bg-grey-lighten-4 pa-3">
                  <span
                    class="text-caption text-grey-darken-1 font-weight-bold ml-2 text-truncate"
                    style="max-width: 60%"
                  >
                    : {{ banner.link || " " }}
                  </span>
                  <v-spacer></v-spacer>
                  <v-switch
                    v-model="banner.isActive"
                    color="success"
                    hide-details
                    density="compact"
                    class="mr-2"
                  ></v-switch>
                  <v-btn
                    icon
                    size="small"
                    color="primary"
                    variant="text"
                    @click="editItem(banner)"
                  >
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                  <v-btn
                    icon
                    size="small"
                    color="error"
                    variant="text"
                    @click="openDeleteDialog(banner)"
                  >
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-window-item>
      </v-window>

      <v-dialog v-model="dialog" max-width="600px" persistent>
        <v-card class="rounded-xl pa-2">
          <v-card-title
            class="text-h5 font-weight-bold pt-4 px-4"
            style="color: rgb(14, 22, 67)"
          >
            {{ isEditing ? "" : "" }}
            {{ activeTab === "sliders" ? " " : " " }}
          </v-card-title>
          <v-divider class="my-3"></v-divider>

          <v-card-text>
            <v-form ref="form" v-model="validForm">
              <v-row>
                <template v-if="activeTab === 'sliders'">
                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.title"
                      label="  (:  )"
                      variant="outlined"
                      color="primary"
                      :rules="[(v) => !!v || ' ']"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.subtitle"
                      label="  (:    50%)"
                      variant="outlined"
                      color="primary"
                    ></v-text-field>
                  </v-col>
                </template>

                <template v-if="activeTab === 'banners'">
                  <v-col cols="12">
                    <v-select
                      v-model="editedItem.position"
                      :items="[
                        { title: ' ', value: 'top' },
                        { title: ' ', value: 'middle' },
                        { title: ' ', value: 'bottom' },
                      ]"
                      label="  "
                      variant="outlined"
                      color="primary"
                      :rules="[(v) => !!v || '   ']"
                    ></v-select>
                  </v-col>
                </template>

                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.link"
                    label="  (    )"
                    variant="outlined"
                    color="primary"
                    placeholder=": /category/electronics"
                  ></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-file-input
                    v-model="imageFile"
                    :label="
                      activeTab === 'sliders'
                        ? '   (  )'
                        : '  '
                    "
                    variant="outlined"
                    color="primary"
                    accept="image/*"
                    prepend-icon=""
                    prepend-inner-icon="mdi-camera"
                    @update:modelValue="handleImageUpload"
                    :rules="[
                      (v) => isEditing || !!previewImage || ' ',
                    ]"
                  ></v-file-input>
                </v-col>

                <v-col
                  cols="12"
                  class="d-flex justify-center"
                  v-if="previewImage"
                >
                  <v-card elevation="2" class="rounded-lg pa-1 w-100">
                    <v-img
                      :src="previewImage"
                      height="150"
                      cover
                      class="rounded"
                    ></v-img>
                  </v-card>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-4">
            <v-spacer></v-spacer>
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-4 font-weight-bold"
              @click="closeDialog"
              ></v-btn
            >
            <v-btn
              color="primary"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              :disabled="!validForm"
              @click="saveItem"
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="400px">
        <v-card class="rounded-xl pa-4 text-center">
          <v-icon color="error" size="60" class="mb-3"
            >mdi-alert-circle-outline</v-icon
          >
          <v-card-title class="text-h6 font-weight-bold text-wrap">
                
            {{ activeTab === "sliders" ? " " : " " }}
          </v-card-title>
          <v-card-actions class="d-flex justify-center mt-4">
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-6 font-weight-bold"
              @click="dialogDelete = false"
              ></v-btn
            >
            <v-btn
              color="error"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              @click="confirmDelete"
              > </v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref } from "vue";

//  
const activeTab = ref("sliders");

// ---    ---
const sliders = ref([
  {
    id: 1,
    title: "  ",
    subtitle: "   40%    ",
    image:
      "https://images.unsplash.com/photo-1607082348824-0a96f2a4b9da?q=80&w=2070&auto=format&fit=crop",
    link: "/shop/sale",
    isActive: true,
  },
  {
    id: 2,
    title: "  ",
    subtitle: "       ",
    image:
      "https://images.unsplash.com/photo-1610945415295-d9bbf067e59c?q=80&w=2071&auto=format&fit=crop",
    link: "/category/smartphones",
    isActive: true,
  },
]);

// ---    ---
const banners = ref([
  {
    id: 1,
    position: "middle",
    image:
      "https://images.unsplash.com/photo-1550009158-9ebf69173e03?q=80&w=2001&auto=format&fit=crop",
    link: "/category/laptops",
    isActive: true,
  },
  {
    id: 2,
    position: "bottom",
    image:
      "https://images.unsplash.com/photo-1505740420928-5e560c06d30e?q=80&w=2070&auto=format&fit=crop",
    link: "/category/audio",
    isActive: false,
  },
]);

// ---    ---
const dialog = ref(false);
const dialogDelete = ref(false);
const isEditing = ref(false);
const validForm = ref(false);
const itemToDelete = ref(null);

const imageFile = ref(null);
const previewImage = ref("");

//  
const defaultItem = {
  id: null,
  title: "",
  subtitle: "",
  link: "",
  image: "",
  position: "top",
  isActive: true,
};
const editedItem = ref({ ...defaultItem });

//   
const handleImageUpload = (fileArray) => {
  const file = Array.isArray(fileArray) ? fileArray[0] : fileArray;
  if (file) {
    previewImage.value = URL.createObjectURL(file);
    editedItem.value.image = previewImage.value;
  } else {
    if (!isEditing.value) {
      previewImage.value = "";
      editedItem.value.image = "";
    }
  }
};

const openAddDialog = () => {
  isEditing.value = false;
  editedItem.value = { ...defaultItem };
  imageFile.value = null;
  previewImage.value = "";
  dialog.value = true;
};

const editItem = (item) => {
  isEditing.value = true;
  editedItem.value = { ...item };
  imageFile.value = null;
  previewImage.value = item.image;
  dialog.value = true;
};

const closeDialog = () => {
  dialog.value = false;
  setTimeout(() => {
    editedItem.value = { ...defaultItem };
    imageFile.value = null;
    previewImage.value = "";
  }, 300);
};

const saveItem = () => {
  const listToUpdate = activeTab.value === "sliders" ? sliders : banners;

  if (isEditing.value) {
    const index = listToUpdate.value.findIndex(
      (item) => item.id === editedItem.value.id
    );
    if (index !== -1) listToUpdate.value[index] = { ...editedItem.value };
  } else {
    editedItem.value.id = Date.now();
    listToUpdate.value.unshift({ ...editedItem.value });
  }
  closeDialog();
};

const openDeleteDialog = (item) => {
  itemToDelete.value = item;
  dialogDelete.value = true;
};

const confirmDelete = () => {
  if (activeTab.value === "sliders") {
    sliders.value = sliders.value.filter((s) => s.id !== itemToDelete.value.id);
  } else {
    banners.value = banners.value.filter((b) => b.id !== itemToDelete.value.id);
  }
  dialogDelete.value = false;
  itemToDelete.value = null;
};
</script>

