              </v-btn>
            </template>
            <v-list density="compact" max-height="250" rounded="lg">
              <v-list-item
                v-if="selectedSeller"
                @click="selectedSeller = null"
                title=" "
                color="error"
                class="text-error"
              >
                <template v-slot:prepend>
                  <v-icon icon="mdi-close" color="error"></v-icon>
                </template>
              </v-list-item>
              <v-list-item
                v-for="n in 5"
                :key="n"
                :title="`  ${n}`"
                :value="n"
                @click="selectedSeller = `  ${n}`"
                :active="selectedSeller === `  ${n}`"
                color="rgb(14 22 67)"
              ></v-list-item>
            </v-list>
          </v-menu>
        </div>

        <div class="d-flex align-center gap-2 export-group">
          <v-btn
            :color="
              isSelectionMode && selectedItems.length > 0 ? '#fff' : '#ffca28'
            "
            variant="outlined"
            rounded="pill"
            class="text-body-2 font-weight-bold"
            :prepend-icon="
              isSelectionMode && selectedItems.length === 0
                ? 'mdi-close'
                : 'mdi-download-box-outline'
            "
            @click="handleExportSelected"
            size="small"
          >
            {{ getExportButtonText }}
          </v-btn>
          <v-btn
            color="#ffca28"
            variant="flat"
            rounded="pill"
            class="text-body-2 font-weight-bold text-black"
            prepend-icon="mdi-download-multiple"
            @click="exportCSV(filteredProducts)"
            size="small"
          >
             
          </v-btn>
        </div>
      </div>
    </v-sheet>

    <div class="Product-section">
      <v-row
        v-if="paginatedProducts.length === 0"
        justify="center"
        class="py-15"
      >
        <v-col cols="12" class="text-center">
          <v-icon size="80" color="grey-lighten-2" class="mb-4"
            >mdi-package-variant-closed</v-icon
          >
          <h3 class="text-h4 font-weight-bold text-grey-darken-1">
                
          </h3>
          <p class="text-grey mt-2">
                   .
          </p>
        </v-col>
      </v-row>

      <v-container v-else class="pa-0">
        <v-row>
          <v-col
            cols="12"
            sm="6"
            md="4"
            lg="3"
            v-for="product in paginatedProducts"
