                    half-increments
                    size="small"
                  ></v-rating>
                  <span class="text-caption text-grey ml-2"
                    >({{ product.rating }})</span
                  >
                </div>

                <div class="mt-auto">
                  <div class="d-flex align-center justify-center mb-1 gap-2">
                    <span class="text-h6 font-weight-black text-primary-dark">
                      ${{
                        Math.ceil(
                          product.price -
                            product.price * (product.discountPercentage / 100)
                        )
                      }}
                    </span>
                    <del
