<script setup>
import { ProductModule } from "@/store/Products.js";
import PopUp from "./PopUp.vue";
import { usePopUpQuickView } from "@/store/PopUp";
import { onMounted, watch, ref, computed } from "vue";
import { AddFavProduct } from "../store/Favourate";

// --- Setup Stores ---
const QuickVeiw = usePopUpQuickView();
const flashDeals = ProductModule();
const cartStore = AddFavProduct();
const FavItem = cartStore.FavItem;

// --- Helper Functions ---
const shortDescription = (text) => {
  if (!text) return "";
  return text.length > 60 ? text.substring(0, 55) + "..." : text;
};
const shortTitle = (text) => {
  if (!text) return "";
  return text.length > 25 ? text.substring(0, 25) + "..." : text;
};

// --- Filter Logic States ---
const selectedCommission = ref(null);
const commissionOptions = [
  { title: " ", value: "high" },
  { title: " ", value: "low" },
];

const selectedPrice = ref(null);
const priceOptions = [
  { title: "  ", value: "asc" },
  { title: "  ", value: "desc" },
];

const selectedSeller = ref(null);

// --- Selection Logic for Export ---
const selectedItems = ref([]);
const isSelectionMode = ref(false);

const getExportButtonText = computed(() => {
  if (!isSelectionMode.value) return " ";
  if (selectedItems.value.length === 0) return "";
  return ` (${selectedItems.value.length})`;
});

const handleExportSelected = () => {
  if (!isSelectionMode.value) {
    isSelectionMode.value = true;
  } else {
    if (selectedItems.value.length > 0) {
      exportCSV(selectedItems.value);
      isSelectionMode.value = false;
      selectedItems.value = [];
    } else {
      isSelectionMode.value = false;
    }
  }
};

const exportCSV = (dataToExport) => {
  if (!dataToExport || dataToExport.length === 0) return;

  const headers = [
    "ID",
    "",
    " ",
    "",
    " ",
    "",
  ];

  const rows = dataToExport.map((product) => {
    const finalPrice = Math.ceil(
      product.price - product.price * (product.discountPercentage / 100)
    );
    return [
      product.id,
      `"${product.title.replace(/"/g, '""')}"`,
      product.price,
      `${product.discountPercentage}%`,
      finalPrice,
      product.rating,
    ];
  });

  const csvContent =
    "\uFEFF" +
    [headers.join(","), ...rows.map((row) => row.join(","))].join("\n");

  const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
  const url = URL.createObjectURL(blob);
  const link = document.createElement("a");
  link.setAttribute("href", url);
  link.setAttribute("download", "_.csv");
  link.style.visibility = "hidden";
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};

// --- Pagination Logic ---
const currentPage = ref(1);
const itemsPerPage = 12;

const filteredProducts = computed(() => {
  let products = flashDeals.flashDeals ? [...flashDeals.flashDeals] : [];

  if (selectedSeller.value) {
    const sellerNum = parseInt(selectedSeller.value.replace(/\D/g, ""));
    if (!isNaN(sellerNum)) {
      products = products.filter((p) => (p.id % 5) + 1 === sellerNum);
    }
  }

  if (selectedPrice.value) {
    const isAsc = selectedPrice.value.value === "asc";
    products.sort((a, b) => {
      const priceA = a.price * (1 - a.discountPercentage / 100);
      const priceB = b.price * (1 - b.discountPercentage / 100);
      return isAsc ? priceA - priceB : priceB - priceA;
    });
  }

  if (selectedCommission.value) {
    const isHigh = selectedCommission.value.value === "high";
    products.sort((a, b) => {
      return isHigh
        ? b.discountPercentage - a.discountPercentage
        : a.discountPercentage - b.discountPercentage;
    });
  }

  return products;
});

watch([selectedSeller, selectedPrice, selectedCommission], () => {
  currentPage.value = 1;
});

const totalPages = computed(() => {
  return Math.ceil(filteredProducts.value.length / itemsPerPage);
});

const paginatedProducts = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage;
  const end = start + itemsPerPage;
  return filteredProducts.value.slice(start, end);
});

// --- Favourites Logic ---
const toggleFavourite = (product) => {
  const exists = cartStore.FavItem.find((p) => p.id === product.id);
  if (exists) {
    cartStore.removeFromCart(product.id);
  } else {
    cartStore.AddFavProductItem({ ...product, quantity: 1 });
  }
};

const isFavourite = (id) => cartStore.FavItem.some((p) => p.id === id);

watch(
  () => FavItem,
  (newVal) => {
    localStorage.setItem("cart-favourate", JSON.stringify(newVal));
  },
  { deep: true }
);

onMounted(async () => {
  await flashDeals.getProducts();
});
</script>

