<template>
  <div class="myhome d-none">
    <div class="todo-container">
      <h2>  </h2>
      <form action="#" @submit.prevent="addToDo">
        <div class="form-group">
          <label> :</label>
          <input
            v-model="todoObject.text"
            type="text"
            placeholder="   "
            required
          />
        </div>

        <div class="form-group">
          <label> :</label>
          <input v-model="todoObject.from" type="date" required />
        </div>

        <div class="form-group">
          <label>  :</label>
          <input v-model="todoObject.to" type="date" required />
        </div>

        <input type="submit" value="  " />
      </form>
    </div>
  </div>
  <BannerHome />
  <TheFeature />
  <TopOffer />
  <FlashDeal />
  <Categoury />
  <BestSeller />
  <!-- <NewProduct /> -->
  <QualityFeature />
  <BrandS />
  <ShopWithUs />
</template>
<script setup>
import { ref, onMounted } from "vue";
import BannerHome from "@/components/BannerHome.vue";
import TheFeature from "@/components/TheFeature.vue";
import TopOffer from "@/components/TopOffer.vue";
import { ProductModule } from "@/store/Products.js";
import FlashDeal from "@/components/FlashDeal.vue";
import Categoury from "@/components/CateGoury.vue";
// import NewProduct from "@/components/NewProduct.vue";
import QualityFeature from "@/components/QualityFeature.vue";
import BrandS from "@/components/BrandS.vue";
import ShopWithUs from "@/components/ShopWithUs.vue";
import BestSeller from "@/components/BestSeller.vue";
const ProductModules = ProductModule();
const todoList = ref([]);
const todoObject = ref({
  id: "",
  text: "",
  from: "",
  to: "",
  createdAt: "",
  isCompleted: false,
});

const addToDo = () => {
  todoObject.value.id = todoList.value.length + 1;
  todoObject.value.createdAt = new Date();
  todoList.value.push(todoObject.value);
  todoarrList();
  console.log(todoList.value);
  todoObject.value = {
    id: "",
    text: "",
    from: "",
    to: "",
    createdAt: "",
    isCompleted: false,
  };
};

const todoarrList = () => {
  localStorage.setItem("toDo", JSON.stringify(todoList.value));
};

const updateToDo = () => {
  const savedData = localStorage.getItem("toDo");
  if (savedData) {
    todoList.value = JSON.parse(savedData);
  }
};
onMounted(async () => {
  updateToDo();
  await ProductModules.getProducts();
});
</script>
