    orderNumber: "ORD-5094",
    customerName: " ",
    phone: "01234567890",
    address: "    ",
    date: "2023-10-27T18:45:00",
    status: " ",
    subtotal: 840,
    shippingFee: 50,
    total: 890,
    items: [
      {
        name: "  4K",
        price: 840,
        quantity: 1,
        image: "https://cdn.dummyjson.com/product-images/3/thumbnail.jpg",
      },
    ],
  },
  {
    id: 4,
    orderNumber: "ORD-5095",
    customerName: " ",
    phone: "01555555555",
    address: "  ",
    date: "2023-10-28T11:20:00",
    status: "",
    subtotal: 300,
    shippingFee: 50,
    total: 350,
    items: [
      {
        name: "  ",
        price: 300,
        quantity: 1,
        image: "https://cdn.dummyjson.com/product-images/4/thumbnail.jpg",
      },
    ],
  },
  {
    id: 5,
    orderNumber: "ORD-5096",
    customerName: " ",
    phone: "01000000000",
    address: "  ",
    date: "2023-10-29T16:10:00",
    status: "",
    subtotal: 150,
    shippingFee: 0,
    total: 150,
    items: [
      {
        name: "  ",
        price: 150,
        quantity: 1,
        image: "https://cdn.dummyjson.com/product-images/5/thumbnail.jpg",
      },
    ],
  },
]);

// ---    ---
const searchQuery = ref("");
const statusFilter = ref(null);
const statusOptions = [
  " ",
  " ",
  " ",
  "",
  "",
];

// ---   (Dialog) ---
const detailsDialog = ref(false);
const selectedOrder = ref(null);

// ---  (Functions) ---

//  
const formatDate = (dateString) => {
  const options = {
    year: "numeric",
    month: "short",
    day: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  };
  return new Date(dateString).toLocaleDateString("ar-EG", options);
};

//    Chip   
const getStatusColor = (status) => {
  switch (status) {
    case " ":
      return "warning";
    case " ":
      return "info";
    case " ":
      return "primary";
    case "":
      return "success";
    case "":
      return "error";
    default:
      return "grey";
  }
};

//   ( +  )
const filteredOrders = computed(() => {
  return orders.value.filter((order) => {
    //   (  )
    const matchesSearch =
      order.orderNumber
        .toLowerCase()
        .includes(searchQuery.value.toLowerCase()) ||
      order.customerName
        .toLowerCase()
        .includes(searchQuery.value.toLowerCase());

    //  
    const matchesStatus = statusFilter.value
      ? order.status === statusFilter.value
      : true;

    return matchesSearch && matchesStatus;
  });
});

//   
const openOrderDetails = (order) => {
  selectedOrder.value = { ...order }; //       
  detailsDialog.value = true;
};

//      
const updateOrderStatus = (order) => {
  const index = orders.value.findIndex((o) => o.id === order.id);
  if (index !== -1) {
    orders.value[index].status = order.status;
  }
};

//   ()
const printInvoice = () => {
  window.print();
};
</script>

