<template>
  <v-locale-provider rtl>
    <v-container fluid class="pa-6" dir="rtl">
      <v-card class="mb-6 rounded-xl" elevation="1">
        <v-card-text
          class="d-flex align-center justify-space-between flex-wrap gap-4 pa-4"
        >
          <div class="d-flex align-center gap-3">
            <v-avatar color="rgb(14, 22, 67)" size="48" class="rounded-lg">
              <v-icon color="white" size="28">mdi-ticket-percent</v-icon>
            </v-avatar>
            <h2
              class="text-h5 font-weight-black"
              style="color: rgb(14, 22, 67)"
            >
               
            </h2>
          </div>

          <div
            class="d-flex align-center gap-3 flex-grow-1 flex-wrap"
            style="max-width: 650px"
          >
            <v-text-field
              v-model="searchQuery"
              prepend-inner-icon="mdi-magnify"
              label="  ..."
              variant="outlined"
              density="compact"
              hide-details
              class="bg-white rounded-lg flex-grow-1"
            ></v-text-field>

            <v-btn
              color="primary"
              prepend-icon="mdi-plus"
              class="font-weight-bold px-6 rounded-lg shadow-btn"
              height="44"
              @click="openAddDialog"
            >
               
            </v-btn>
          </div>
        </v-card-text>
      </v-card>

      <v-row class="mb-4">
        <v-col cols="12" md="4">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-primary"
            elevation="1"
          >
            <v-avatar
              color="primary-lighten-4"
              size="50"
              class="mr-3 text-primary"
            >
              <v-icon>mdi-ticket-confirmation</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                 
              </div>
              <div class="text-h5 font-weight-black">
                {{ activeCouponsCount }}
              </div>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="4">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-success"
            elevation="1"
          >
            <v-avatar
              color="green-lighten-4"
              size="50"
              class="mr-3 text-success"
            >
              <v-icon>mdi-check-decagram</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                  
              </div>
              <div class="text-h5 font-weight-black text-success">
                {{ totalUses }}
              </div>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="4">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-error"
            elevation="1"
          >
            <v-avatar color="red-lighten-4" size="50" class="mr-3 text-error">
              <v-icon>mdi-calendar-remove</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                  
              </div>
              <div class="text-h5 font-weight-black text-error">
                {{ expiredCouponsCount }}
              </div>
            </div>
          </v-card>
        </v-col>
      </v-row>

      <v-card class="rounded-xl overflow-hidden" elevation="1">
        <v-table class="coupons-table pa-2">
          <thead class="bg-grey-lighten-4">
            <tr>
              <th class="text-right font-weight-bold"> </th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"> </th>
              <th class="text-center font-weight-bold"></th>
              <th class="text-center font-weight-bold"></th>
              <th class="text-center font-weight-bold"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredCoupons.length === 0">
              <td colspan="7" class="text-center pa-6 text-grey-darken-1">
                   .
              </td>
            </tr>

            <tr
              v-for="coupon in filteredCoupons"
              :key="coupon.id"
              class="hover-row"
            >
              <td>
                <v-chip
                  color="rgb(14, 22, 67)"
                  variant="tonal"
                  class="font-weight-black text-uppercase"
                  size="large"
                >
                  <v-icon left size="18" class="mr-2">mdi-content-copy</v-icon>
                  {{ coupon.code }}
                </v-chip>
              </td>

              <td class="text-center text-grey-darken-1 font-weight-medium">
                {{
                  coupon.type === "percentage"
                    ? "  (%)"
                    : "  ($)"
                }}
              </td>

              <td
                class="text-center font-weight-black text-success text-subtitle-1"
              >
                {{
                  coupon.type === "percentage"
                    ? coupon.value + "%"
                    : "$" + coupon.value
                }}
              </td>

              <td
                class="text-center"
                :class="
                  isExpired(coupon.expiryDate)
                    ? 'text-error font-weight-bold'
                    : 'text-grey-darken-1'
                "
              >
                {{ formatDate(coupon.expiryDate) }}
              </td>

              <td class="text-center">
                <span
                  class="font-weight-bold"
                  :class="
                    coupon.used >= coupon.limit ? 'text-error' : 'text-primary'
                  "
                >
                  {{ coupon.used }}
                </span>
                <span class="text-grey"> / {{ coupon.limit }}</span>
              </td>

              <td class="text-center">
                <v-switch
                  v-model="coupon.isActive"
                  color="success"
                  hide-details
                  density="compact"
                  class="d-inline-flex"
                  :disabled="
                    isExpired(coupon.expiryDate) || coupon.used >= coupon.limit
                  "
                ></v-switch>
              </td>

              <td class="text-center">
                <v-btn
                  icon
                  size="small"
                  color="primary"
                  variant="text"
                  @click="editItem(coupon)"
                >
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                  icon
                  size="small"
                  color="error"
                  variant="text"
                  @click="openDeleteDialog(coupon)"
                >
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>

      <v-dialog v-model="dialog" max-width="500px" persistent>
        <v-card class="rounded-xl pa-2">
          <v-card-title
            class="text-h5 font-weight-bold pt-4 px-4"
            style="color: rgb(14, 22, 67)"
          >
            {{ isEditing ? " " : "  " }}
          </v-card-title>
          <v-divider class="my-3"></v-divider>

          <v-card-text>
            <v-form ref="form" v-model="validForm">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.code"
                    label="  (: SUMMER50)"
                    variant="outlined"
                    color="primary"
                    class="text-uppercase"
                    :rules="[(v) => !!v || '  ']"
                  >
                    <template v-slot:append-inner>
                      <v-btn
                        icon="mdi-refresh"
                        variant="text"
                        size="small"
                        color="primary"
                        @click="generateRandomCode"
                        title="  "
                      ></v-btn>
                    </template>
                  </v-text-field>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-select
                    v-model="editedItem.type"
                    :items="[
                      { title: '  (%)', value: 'percentage' },
                      { title: '  ($)', value: 'fixed' },
                    ]"
                    label=" "
                    variant="outlined"
                    color="primary"
                  ></v-select>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.number="editedItem.value"
                    :label="
                      editedItem.type === 'percentage'
                        ? ' (%)'
                        : ' ($)'
                    "
                    type="number"
                    variant="outlined"
                    color="primary"
                    :rules="[
                      (v) => !!v || ' ',
                      (v) => v > 0 || '      ',
                      (v) =>
                        (editedItem.type === 'percentage' ? v <= 100 : true) ||
                        '     100%',
                    ]"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.number="editedItem.limit"
                    label="  ( )"
                    type="number"
                    variant="outlined"
                    color="primary"
                    :rules="[
                      (v) => !!v || ' ',
                      (v) => v > 0 || '     ',
                    ]"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="editedItem.expiryDate"
                    label=" "
                    type="date"
                    variant="outlined"
                    color="primary"
                    :rules="[(v) => !!v || ' ']"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-4">
            <v-spacer></v-spacer>
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-4 font-weight-bold"
              @click="closeDialog"
              ></v-btn
            >
            <v-btn
              color="primary"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              :disabled="!validForm"
              @click="saveItem"
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="400px">
        <v-card class="rounded-xl pa-4 text-center">
          <v-icon color="error" size="60" class="mb-3"
            >mdi-alert-circle-outline</v-icon
          >
          <v-card-title class="text-h6 font-weight-bold text-wrap"
            >      </v-card-title
          >
          <v-card-actions class="d-flex justify-center mt-4">
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-6 font-weight-bold"
              @click="dialogDelete = false"
              ></v-btn
            >
            <v-btn
              color="error"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              @click="confirmDelete"
              > </v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref, computed } from "vue";

// ---    ---
const coupons = ref([
  {
    id: 1,
    code: "WELCOME20",
    type: "percentage",
    value: 20,
    expiryDate: "2026-12-31",
    limit: 1000,
    used: 450,
    isActive: true,
  },
  {
    id: 2,
    code: "WINTER50",
    type: "fixed",
    value: 50,
    expiryDate: "2026-02-28",
    limit: 100,
    used: 100,
    isActive: false,
  }, // 
  {
    id: 3,
    code: "FLASH10",
    type: "percentage",
    value: 10,
    expiryDate: "2023-11-25",
    limit: 500,
    used: 320,
    isActive: false,
  }, //  
  {
    id: 4,
    code: "VIP2026",
    type: "fixed",
    value: 100,
    expiryDate: "2026-06-30",
    limit: 50,
    used: 12,
    isActive: true,
  },
]);

// ---    ---
const searchQuery = ref("");

// ---   (Dialogs) ---
const dialog = ref(false);
const dialogDelete = ref(false);
const isEditing = ref(false);
const validForm = ref(false);
const itemToDelete = ref(null);

//   
const defaultItem = {
  id: null,
  code: "",
  type: "percentage",
  value: 0,
  limit: 100,
  used: 0,
  expiryDate: "",
  isActive: true,
};

const editedItem = ref({ ...defaultItem });

// ---  (Computed) ---
//    
const isExpired = (dateString) => {
  return new Date(dateString) < new Date(new Date().setHours(0, 0, 0, 0));
};

const activeCouponsCount = computed(() => {
  return coupons.value.filter(
    (c) => c.isActive && !isExpired(c.expiryDate) && c.used < c.limit
  ).length;
});

const expiredCouponsCount = computed(() => {
  return coupons.value.filter((c) => isExpired(c.expiryDate)).length;
});

const totalUses = computed(() => {
  return coupons.value.reduce((sum, coupon) => sum + coupon.used, 0);
});

//  
const filteredCoupons = computed(() => {
  if (!searchQuery.value) return coupons.value;
  return coupons.value.filter((c) =>
    c.code.toLowerCase().includes(searchQuery.value.toLowerCase())
  );
});

// ---  (Functions) ---

//     
const formatDate = (dateString) => {
  const options = { year: "numeric", month: "short", day: "numeric" };
  return new Date(dateString).toLocaleDateString("ar-EG", options);
};

//   
const generateRandomCode = () => {
  const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
  let result = "";
  for (let i = 0; i < 8; i++) {
    result += chars.charAt(Math.floor(Math.random() * chars.length));
  }
  editedItem.value.code = result;
};

const openAddDialog = () => {
  isEditing.value = false;
  editedItem.value = { ...defaultItem };

  //    (   )
  const nextMonth = new Date();
  nextMonth.setMonth(nextMonth.getMonth() + 1);
  editedItem.value.expiryDate = nextMonth.toISOString().split("T")[0];

  dialog.value = true;
};

const editItem = (item) => {
  isEditing.value = true;
  editedItem.value = { ...item };
  dialog.value = true;
};

const closeDialog = () => {
  dialog.value = false;
  setTimeout(() => {
    editedItem.value = { ...defaultItem };
  }, 300);
};

const saveItem = () => {
  //     
  editedItem.value.code = editedItem.value.code.toUpperCase();

  if (isEditing.value) {
    const index = coupons.value.findIndex((c) => c.id === editedItem.value.id);
    if (index !== -1) coupons.value[index] = { ...editedItem.value };
  } else {
    editedItem.value.id = Date.now();
    coupons.value.unshift({ ...editedItem.value });
  }
  closeDialog();
};

const openDeleteDialog = (item) => {
  itemToDelete.value = item;
  dialogDelete.value = true;
};

const confirmDelete = () => {
  coupons.value = coupons.value.filter((c) => c.id !== itemToDelete.value.id);
  dialogDelete.value = false;
  itemToDelete.value = null;
};
</script>

