            </v-btn>
          </div>
        </v-card-text>
      </v-card>

      <v-row class="mb-4">
        <v-col cols="12" md="6">
          <v-card
            class="rounded-xl pa-4 border-left-success h-100 d-flex flex-column justify-center"
