          >
            <div class="d-flex justify-space-between align-center mb-3">
              <div class="d-flex align-center gap-2">
                <v-avatar
                  color="green-lighten-4"
                  size="40"
                  class="text-success"
                >
                  <v-icon>mdi-gift-outline</v-icon>
                </v-avatar>
                <span class="text-subtitle-1 font-weight-bold"
                  >  </span
                >
              </div>
              <v-switch
                v-model="isFreeShippingEnabled"
                color="success"
                hide-details
                density="compact"
              ></v-switch>
            </div>
            <div
              class="d-flex align-center gap-3"
              :class="{ 'opacity-50': !isFreeShippingEnabled }"
            >
              <span class="text-grey-darken-1 text-subtitle-2"
                >     :</span
              >
              <div class="d-flex align-center" style="max-width: 150px">
                <v-text-field
                  v-model.number="freeShippingThreshold"
                  variant="outlined"
                  density="compact"
                  hide-details
                  type="number"
                  suffix="."
                  :disabled="!isFreeShippingEnabled"
                  class="font-weight-bold bg-white"
                ></v-text-field>
              </div>
              <v-btn
                size="small"
                color="success"
                variant="tonal"
                class="font-weight-bold"
                :disabled="!isFreeShippingEnabled"
                ></v-btn
              >
            </div>
          </v-card>
        </v-col>

        <v-col cols="12" md="3">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-primary h-100"
            elevation="1"
          >
            <v-avatar
              color="primary-lighten-4"
              size="50"
              class="mr-3 text-primary"
            >
              <v-icon>mdi-map-marker-multiple-outline</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                 
              </div>
              <div class="text-h5 font-weight-black">
                {{ zones.length }}
                <span class="text-caption text-grey"></span>
              </div>
            </div>
          </v-card>
        </v-col>

        <v-col cols="12" md="3">
          <v-card
            class="rounded-xl pa-4 d-flex align-center border-left-warning h-100"
            elevation="1"
          >
            <v-avatar
              color="orange-lighten-4"
              size="50"
              class="mr-3 text-warning"
            >
              <v-icon>mdi-cash-fast</v-icon>
            </v-avatar>
            <div>
              <div class="text-subtitle-2 text-grey-darken-1">
                  
              </div>
              <div class="text-h5 font-weight-black text-warning">
                {{ averageShippingCost }}
                <span class="text-caption text-grey">.</span>
              </div>
            </div>
          </v-card>
        </v-col>
      </v-row>

      <v-card class="rounded-xl overflow-hidden" elevation="1">
        <v-table class="custom-table pa-2">
          <thead class="bg-grey-lighten-4">
            <tr>
              <th class="text-right font-weight-bold"> / </th>
              <th class="text-center font-weight-bold">  (.)</th>
              <th class="text-center font-weight-bold">  </th>
              <th class="text-center font-weight-bold"></th>
              <th class="text-center font-weight-bold"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="zones.length === 0">
              <td colspan="5" class="text-center pa-6 text-grey-darken-1">
                      .
              </td>
            </tr>

            <tr v-for="zone in zones" :key="zone.id" class="hover-row">
              <td
                class="font-weight-bold py-3 text-subtitle-2"
                style="color: rgb(14, 22, 67)"
              >
                <v-icon color="primary" size="20" class="mr-1 ml-2"
                  >mdi-map-marker-radius</v-icon
                >
                {{ zone.name }}
              </td>

              <td class="text-center">
                <span
                  class="font-weight-black text-subtitle-1"
                  :class="zone.cost === 0 ? 'text-success' : 'text-primary'"
                >
                  {{ zone.cost === 0 ? "" : zone.cost }}
                </span>
              </td>

              <td class="text-center text-grey-darken-1 font-weight-medium">
                <v-icon size="16" class="ml-1 text-grey"
                  >mdi-clock-outline</v-icon
                >
                {{ zone.time }}
              </td>

              <td class="text-center">
                <v-switch
                  v-model="zone.isActive"
                  color="success"
                  hide-details
                  density="compact"
                  class="d-inline-flex"
                ></v-switch>
              </td>

              <td class="text-center">
                <v-btn
                  icon
                  size="small"
                  color="primary"
                  variant="text"
                  @click="editItem(zone)"
                  title=""
                >
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                  icon
                  size="small"
                  color="error"
                  variant="text"
                  @click="openDeleteDialog(zone)"
                  title=""
                >
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>

      <v-dialog v-model="dialog" max-width="500px" persistent>
        <v-card class="rounded-xl pa-2">
          <v-card-title
            class="text-h5 font-weight-bold pt-4 px-4"
            style="color: rgb(14, 22, 67)"
          >
            {{ isEditing ? "  " : "   " }}
          </v-card-title>
          <v-divider class="my-3"></v-divider>

          <v-card-text>
            <v-form ref="form" v-model="validForm">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.name"
                    label="  /  (:  )"
                    variant="outlined"
                    color="primary"
                    :rules="[(v) => !!v || '  ']"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.number="editedItem.cost"
                    label="  (.)"
                    type="number"
                    variant="outlined"
                    color="primary"
                    :rules="[
                      (v) => (v !== null && v !== '') || ' ',
                      (v) => v >= 0 || '     ',
                    ]"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="editedItem.time"
                    label="  (: 2-3  )"
                    variant="outlined"
                    color="primary"
                    :rules="[(v) => !!v || '  ']"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-4">
            <v-spacer></v-spacer>
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-4 font-weight-bold"
              @click="closeDialog"
              ></v-btn
            >
            <v-btn
              color="primary"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              :disabled="!validForm"
              @click="saveItem"
            >
               
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="400px">
        <v-card class="rounded-xl pa-4 text-center">
          <v-icon color="error" size="60" class="mb-3"
            >mdi-alert-circle-outline</v-icon
          >
          <v-card-title class="text-h6 font-weight-bold text-wrap"
            >      </v-card-title
          >
          <v-card-text class="text-grey-darken-1"
            >        
            .</v-card-text
          >
          <v-card-actions class="d-flex justify-center mt-4">
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-6 font-weight-bold"
              @click="dialogDelete = false"
              ></v-btn
            >
            <v-btn
              color="error"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              @click="confirmDelete"
              > </v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref, computed } from "vue";

// ---    ---
const isFreeShippingEnabled = ref(true);
const freeShippingThreshold = ref(1500); // 1500 

// ---     ---
const zones = ref([
  {
    id: 1,
    name: " ",
    cost: 50,
    time: "1-2  ",
    isActive: true,
  },
  { id: 2, name: "", cost: 65, time: "2-3  ", isActive: true },
  {
    id: 3,
    name: "  ",
    cost: 80,
    time: "3-4  ",
    isActive: true,
  },
  {
    id: 4,
    name: " ",
    cost: 110,
    time: "4-6  ",
    isActive: true,
  },
  {
    id: 5,
    name: "  (  )",
    cost: 150,
    time: "5-7  ",
    isActive: false,
  },
]);

// ---    ---
const dialog = ref(false);
const dialogDelete = ref(false);
const isEditing = ref(false);
const validForm = ref(false);
const itemToDelete = ref(null);

const defaultItem = { id: null, name: "", cost: 0, time: "", isActive: true };
const editedItem = ref({ ...defaultItem });

// ---  (Computed) ---
const averageShippingCost = computed(() => {
  if (zones.value.length === 0) return 0;
  const total = zones.value.reduce((sum, zone) => sum + zone.cost, 0);
  return Math.round(total / zones.value.length);
});

// ---  (Functions) ---
const openAddDialog = () => {
  isEditing.value = false;
  editedItem.value = { ...defaultItem };
  dialog.value = true;
};

const editItem = (item) => {
  isEditing.value = true;
  editedItem.value = { ...item };
  dialog.value = true;
};

const closeDialog = () => {
  dialog.value = false;
  setTimeout(() => {
    editedItem.value = { ...defaultItem };
  }, 300);
};

const saveItem = () => {
  if (isEditing.value) {
    const index = zones.value.findIndex((z) => z.id === editedItem.value.id);
    if (index !== -1) zones.value[index] = { ...editedItem.value };
  } else {
    editedItem.value.id = Date.now();
    zones.value.push({ ...editedItem.value });
  }
  closeDialog();
};

const openDeleteDialog = (item) => {
  itemToDelete.value = item;
  dialogDelete.value = true;
};

const confirmDelete = () => {
  zones.value = zones.value.filter((z) => z.id !== itemToDelete.value.id);
  dialogDelete.value = false;
  itemToDelete.value = null;
};
</script>

