<template>
  <v-app theme="light">
    <v-locale-provider rtl>
      <v-main
        class="bg-grey-lighten-5 d-flex align-center justify-center"
        style="min-height: 100vh"
      >
        <v-container>
          <v-row justify="center">
            <v-col cols="12" sm="10" md="8" lg="6">
              <v-card class="rounded-xl elevation-5 overflow-hidden">
                <v-row no-gutters>
                  <v-col
                    cols="12"
                    md="5"
                    class="bg-indigo-darken-3 d-flex flex-column align-center justify-center pa-10 text-center"
                  >
                    <v-icon size="80" color="white" class="mb-4"
                      >mdi-rocket-launch-outline</v-icon
                    >
                    <h2 class="text-h4 font-weight-bold text-white mb-2">
                       !
                    </h2>
                    <p class="text-indigo-lighten-4 mb-6">
                             .
                    </p>
                    <v-btn
                      variant="outlined"
                      color="white"
                      class="rounded-pill px-6"
                      to="/Auth/LogIn"
                    >
                        
                    </v-btn>
                  </v-col>

                  <v-col cols="12" md="7" class="pa-8 pa-md-10 bg-white">
                    <h1
                      class="text-h5 font-weight-bold text-indigo-darken-4 mb-1"
                    >
                        
                    </h1>
                    <p class="text-grey mb-6">    </p>

                    <v-alert
                      v-if="authStore.error"
                      type="error"
                      variant="tonal"
                      density="compact"
                      class="mb-4"
                      closable
                      :text="authStore.error"
                    ></v-alert>

                    <v-form @submit.prevent="handleRegister">
                      <v-text-field
                        v-model="form.name"
                        label=" "
                        prepend-inner-icon="mdi-account-outline"
                        variant="outlined"
                        density="comfortable"
                        color="indigo"
                        class="mb-2"
                      ></v-text-field>

                      <v-text-field
                        v-model="form.email"
                        label=" "
                        prepend-inner-icon="mdi-email-outline"
                        variant="outlined"
                        density="comfortable"
                        color="indigo"
                        class="mb-2"
                        hint=": eve.holt@reqres.in "
                        persistent-hint
                      ></v-text-field>

                      <v-text-field
                        v-model="form.password"
                        label=" "
                        prepend-inner-icon="mdi-lock-outline"
                        :type="showPass ? 'text' : 'password'"
                        :append-inner-icon="
                          showPass ? 'mdi-eye-off' : 'mdi-eye'
                        "
                        @click:append-inner="showPass = !showPass"
                        variant="outlined"
                        density="comfortable"
                        color="indigo"
                        class="mb-2"
                      ></v-text-field>

                      <v-text-field
                        v-model="form.confirmPassword"
                        label="  "
                        prepend-inner-icon="mdi-lock-check-outline"
                        type="password"
                        variant="outlined"
                        density="comfortable"
                        color="indigo"
                        class="mb-6"
                        :error-messages="passwordError"
                      ></v-text-field>

                      <v-btn
                        block
                        color="indigo"
                        size="large"
                        type="submit"
                        class="font-weight-bold rounded-lg elevation-2 mb-4"
                        :loading="authStore.loading"
                        :disabled="authStore.loading"
                      >
                         
                      </v-btn>

                      <div class="text-center text-caption text-grey">
                           
                        <a
                          href="#"
                          class="text-indigo text-decoration-none font-weight-bold"
                          > </a
                        >
                      </div>
                    </v-form>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-locale-provider>
  </v-app>
</template>

<script setup>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { useAuthStore } from "@/store/auth/Register"; //  

const router = useRouter();
const authStore = useAuthStore(); //  

const showPass = ref(false);
const passwordError = ref(""); //      

const form = ref({
  name: "",
  email: "",
  password: "",
  confirmPassword: "",
});

const handleRegister = async () => {
  passwordError.value = ""; //    

  // 1.       
  if (form.value.password !== form.value.confirmPassword) {
    passwordError.value = "   ";
    return;
  }

  // 2.     
  // :  API         
  const success = await authStore.registerUser({
    email: form.value.email,
    password: form.value.password,
  });

  // 3.   
  if (success) {
    //       
    console.log("    ...");
    router.push("/"); //  router.push('/dashboard')
  }
};
</script>
