              class="hover-row"
              :class="{ 'bg-blue-grey-lighten-5': !msg.isRead }"
            >
              <td class="py-3">
                <div class="d-flex align-center gap-3">
                  <v-avatar
                    size="36"
                    :color="msg.isRead ? 'grey-lighten-2' : 'primary'"
