                  </v-avatar>
                  <div>
                    <div
                      class="text-subtitle-2"
                      :class="
                        msg.isRead
                          ? 'font-weight-medium'
                          : 'font-weight-black text-primary'
                      "
                    >
                      {{ msg.name }}
                    </div>
                    <div class="text-caption text-grey-darken-1">
                      {{ msg.email }}
                    </div>
                  </div>
                </div>
              </td>

              <td style="max-width: 250px" class="text-truncate">
                <span
                  :class="
                    msg.isRead
                      ? 'text-grey-darken-2'
                      : 'font-weight-bold text-black'
                  "
                >
                  {{ msg.subject }}
                </span>
                <span class="text-caption text-grey ml-2"
                  >- {{ msg.message.substring(0, 30) }}...</span
                >
              </td>

              <td
                class="text-center text-caption text-grey-darken-1 font-weight-medium"
              >
                {{ formatDate(msg.date) }}
              </td>

              <td class="text-center">
                <v-chip
                  size="small"
                  :color="msg.isRead ? 'grey' : 'error'"
                  :variant="msg.isRead ? 'tonal' : 'flat'"
                  class="font-weight-bold"
                >
                  {{ msg.isRead ? "" : "" }}
                </v-chip>
              </td>

              <td class="text-center">
                <v-btn
                  icon
                  size="small"
                  color="primary"
                  variant="text"
                  @click="openMessage(msg)"
                  title=" "
                >
                  <v-icon>mdi-eye-outline</v-icon>
                </v-btn>
                <v-btn
                  icon
                  size="small"
                  color="error"
                  variant="text"
                  @click="deleteMessage(msg.id)"
                  title=""
                >
                  <v-icon>mdi-delete-outline</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>

      <v-dialog v-model="dialog" max-width="600px">
