<script setup>
import { ref, computed } from "vue";

// ---    ---
const messages = ref([
  {
    id: 1,
    name: " ",
    email: "ahmed@example.com",
    phone: "01011122233",
    subject: "   ",
    message:
      "    iPhone 15 Pro Max    256       ",
    date: "2026-02-21T10:30:00",
    isRead: false,
  },
  {
    id: 2,
    name: " ",
    email: "sara.ali@gmail.com",
    phone: "",
    subject: "  ",
    message: "        ",
    date: "2026-02-20T14:15:00",
    isRead: false,
  },
  {
    id: 3,
    name: " ",
    email: "mohamed99@yahoo.com",
    phone: "01233344455",
    subject: " ",
    message:
      "   #1024          ",
    date: "2026-02-18T09:00:00",
    isRead: true,
  },
  {
    id: 4,
    name: "  ",
    email: "info@techcorp.com",
    phone: "022345678",
    subject: "   ",
    message:
      "      10         .",
    date: "2026-02-15T11:45:00",
    isRead: true,
  },
]);

// ---    ---
const searchQuery = ref("");
const statusFilter = ref("");

// ---   (Dialog) ---
const dialog = ref(false);
const selectedMessage = ref(null);

// ---  (Computed) ---
const unreadCount = computed(
  () => messages.value.filter((m) => !m.isRead).length
);

//  
const filteredMessages = computed(() => {
  return messages.value
    .filter((msg) => {
      //    
      const matchSearch =
        msg.name.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
        msg.email.toLowerCase().includes(searchQuery.value.toLowerCase());

      //  
      let matchStatus = true;
      if (statusFilter.value === " ( )")
        matchStatus = !msg.isRead;
      if (statusFilter.value === "") matchStatus = msg.isRead;

      return matchSearch && matchStatus;
    })
    .sort((a, b) => new Date(b.date) - new Date(a.date)); //    
});

// ---  (Functions) ---

//      
const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString("ar-EG", { month: "short", day: "numeric" });
};

//      
const formatFullDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleString("ar-EG", {
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  });
};

//    
const openMessage = (msg) => {
  selectedMessage.value = msg;
  dialog.value = true;

  //      
  if (!msg.isRead) {
    const index = messages.value.findIndex((m) => m.id === msg.id);
    if (index !== -1) messages.value[index].isRead = true;
  }
};

//  
const deleteMessage = (id) => {
  messages.value = messages.value.filter((m) => m.id !== id);
};
</script>

