import { defineStore } from "pinia";

export const AddInCart = defineStore("AddInCart", {
  state: () => ({
    CartItem: JSON.parse(localStorage.getItem("cart-product")) || [],
  }),

  actions: {
    AddInCartItem(item) {
      const existingItem = this.CartItem.find(
        (product) => product.id == item.id
      );
      if (existingItem) {
        existingItem.quantity += item.quantity;
      } else {
        this.CartItem.push(item);
      }
      localStorage.setItem("cart-product", JSON.stringify(this.CartItem));
      console.log(this.CartItem);
    },
    removeFromCart(id) {
      const index = this.CartItem.findIndex((item) => item.id === id);

      if (index !== -1) {
        this.CartItem.splice(index, 1);
      }

      localStorage.setItem("cart-product", JSON.stringify(this.CartItem));
    },
  },
});
