<template>
  <v-app theme="light">
    <v-locale-provider rtl>
      <v-main class="bg-grey-lighten-5">
        <v-container class="py-10">
          <div class="mb-6">
            <h1 class="text-h4 font-weight-bold text-indigo-darken-3">
                
            </h1>
            <p class="text-grey-darken-1">
                      
            </p>
          </div>

          <v-card class="rounded-xl border-0 mb-8" elevation="2">
            <v-table hover density="comfortable">
              <thead class="bg-indigo-lighten-5">
                <tr>
                  <th
                    class="font-weight-bold text-indigo-darken-4 text-center"
                    width="50"
                  >
                    <v-checkbox-btn
                      v-model="selectAll"
                      @update:model-value="toggleAll"
                    ></v-checkbox-btn>
                  </th>
                  <th class="font-weight-bold text-indigo-darken-4 text-start">
                     
                  </th>
                  <th class="font-weight-bold text-indigo-darken-4 text-center">
                     
                  </th>
                  <th class="font-weight-bold text-indigo-darken-4 text-center">
                    
                  </th>
                  <th class="font-weight-bold text-indigo-darken-4 text-center">
                    
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in orders" :key="item.id">
                  <td class="text-center">
                    <v-checkbox-btn
                      v-model="selectedItems"
                      :value="item.id"
                      color="indigo"
                    ></v-checkbox-btn>
                  </td>
                  <td class="font-weight-medium">{{ item.orderNo }}</td>
                  <td class="text-center text-grey-darken-1">
                    {{ item.date }}
                  </td>
                  <td class="text-center">
                    <span
                      class="text-success font-weight-bold text-caption border px-2 py-1 rounded"
                    >
                      {{ item.status }}
                    </span>
                  </td>
                  <td class="text-center font-weight-bold text-indigo">
                    {{ item.commission }} .
                  </td>
                </tr>
              </tbody>
            </v-table>
          </v-card>

          <v-card class="mb-8 rounded-xl bg-white border pa-6" flat>
            <div
              class="d-flex align-center justify-space-between flex-wrap gap-4"
            >
              <div>
                <div class="text-caption text-grey-darken-1 mb-1">
                    
                </div>
                <div class="text-h4 font-weight-bold text-indigo-darken-3">
                  {{ totalAmount }} <span class="text-h6 text-grey">.</span>
                </div>
              </div>

              <v-btn
                color="indigo"
                size="large"
                class="px-8 rounded-lg font-weight-bold"
                elevation="3"
                prepend-icon="mdi-cash-check"
                :disabled="selectedItems.length === 0"
              >
                 
              </v-btn>
            </div>
          </v-card>

          <div class="mt-8">
            <h3 class="text-h6 font-weight-bold text-grey-darken-3 mb-4">
                
            </h3>
            <v-card class="rounded-xl border-0" elevation="1">
              <v-table density="compact">
                <thead class="bg-grey-lighten-4">
                  <tr>
                    <th class="text-start"> </th>
                    <th class="text-center"></th>
                    <th class="text-center"></th>
                    <th class="text-end"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="history in historyList" :key="history.id">
                    <td>#{{ history.id }}</td>
                    <td class="text-center font-weight-bold">
                      {{ history.amount }} .
                    </td>
                    <td class="text-center">
                      <v-chip
                        size="x-small"
                        :color="history.status === '' ? 'green' : 'orange'"
                      >
                        {{ history.status }}
                      </v-chip>
                    </td>
                    <td class="text-end text-grey">{{ history.date }}</td>
                  </tr>
                </tbody>
              </v-table>
            </v-card>
          </div>
        </v-container>
      </v-main>
    </v-locale-provider>
  </v-app>
</template>

<script setup>
import { ref, computed } from "vue";

// 
const orders = ref([
  {
    id: 1,
    orderNo: "N857875",
    status: "",
    commission: 500,
    date: "2023-10-01",
  },
  {
    id: 2,
    orderNo: "N857876",
    status: "",
    commission: 150,
    date: "2023-10-02",
  },
  {
    id: 3,
    orderNo: "N857877",
    status: "",
    commission: 300,
    date: "2023-10-03",
  },
  {
    id: 4,
    orderNo: "N857880",
    status: "",
    commission: 500,
    date: "2023-10-05",
  },
]);

const historyList = [
  { id: 101, amount: 650, status: "", date: "02/02/2022" },
  { id: 102, amount: 120, status: "", date: "05/02/2022" },
];

//   
const selectedItems = ref([]); //  IDs  
const selectAll = ref(false);

//   
const toggleAll = () => {
  if (selectAll.value) {
    selectedItems.value = orders.value.map((item) => item.id);
  } else {
    selectedItems.value = [];
  }
};

//   
const totalAmount = computed(() => {
  return orders.value
    .filter((item) => selectedItems.value.includes(item.id))
    .reduce((sum, item) => sum + item.commission, 0);
});
</script>
