<script setup>
import { ref, reactive, computed } from "vue";

// 1.    
//        
const bootstrapInput = {
  variant: "outlined",
  density: "compact",
  color: "primary",
  bgColor: "white",
  hideDetails: "auto",
};

const valid = ref(false);

//  
const form = reactive({
  orderType: null,
  phone: "",
  otherPhone: "",
  fullName: "",
  governorate: null,
  city: null,
  address: "",
  moderator: "",
  leadSource: "",
  notes: "",
  description: "",
  hasCustomImage: false,
  images: [{ file: null, desc: "" }], //   
  orderItems: [],
  shippingType: "now",
  shippingDate: "",
});

//   
const governorates = [
  { id: 1, name: "", cities: [" ", "", ""] },
  { id: 2, name: "", cities: ["", "", ""] },
  { id: 3, name: "", cities: ["", ""] },
];

const availableCities = computed(() => {
  const gov = governorates.find((g) => g.id === form.governorate);
  return gov ? gov.cities : [];
});

const productsList = [
  { name: "  ", size: "L", color: "", price: 150, commission: 20 },
  {
    name: " ",
    size: "32",
    color: "",
    price: 250,
    commission: 35,
  },
  { name: " ", size: "42", color: "", price: 400, commission: 50 },
];

const selectedProduct = ref(null);

//   
const addImageRow = () => {
  form.images.push({ file: null, desc: "" });
};

const removeImageRow = () => {
  if (form.images.length > 0) {
    form.images.pop();
  }
};

//   
const addProductRow = (product) => {
  if (product) {
    form.orderItems.push({
      ...product,
      qty: 1,
    });
    selectedProduct.value = null; // Reset search
  }
};

const removeProduct = (index) => {
  form.orderItems.splice(index, 1);
};

// 
const totalOrder = computed(() => {
  return form.orderItems.reduce((acc, item) => acc + item.price * item.qty, 0);
});

const totalCommission = computed(() => {
  return form.orderItems.reduce(
    (acc, item) => acc + item.commission * item.qty,
    0
  );
});

const totalPaid = computed(() => {
  return totalOrder.value + 50; // 50      
});

// 
const rules = {
  required: (value) => !!value || "  ",
  phone: (value) =>
    (value && value.length === 11) || "    11 ",
};

const submitOrder = () => {
  if (valid.value) {
    alert("    ()");
    console.log(form);
  }
};
</script>

