<template>
  <v-app theme="light">
    <v-locale-provider rtl>
      <v-main
        class="bg-grey-lighten- d-flex align-center justify-center"
        style="min-height: 100vh"
      >
        <v-container style="margin-top: -109px">
          <v-row justify="center">
            <v-col cols="12" sm="10" md="8" lg="6">
              <v-card class="rounded-xl elevation-5 overflow-hidden">
                <v-row no-gutters>
                  <v-col
                    cols="12"
                    md="7"
                    class="pa-8 pa-md-10 bg-white order-2 order-md-1"
                  >
                    <div class="mb-6">
                      <h1 class="text-h5 font-weight-bold text-indigo-darken-4">
                          
                      </h1>
                      <p class="text-grey-darken-1">
                            
                      </p>
                    </div>

                    <v-form @submit.prevent="handleLogin">
                      <v-text-field
                        v-model="form.email"
                        label=" "
                        placeholder="example@mail.com"
                        prepend-inner-icon="mdi-email-outline"
                        variant="outlined"
                        density="comfortable"
                        color="indigo"
                        class="mb-2"
                      ></v-text-field>

                      <div class="mb-1">
                        <v-text-field
                          v-model="form.password"
                          label=" "
                          prepend-inner-icon="mdi-lock-outline"
                          :type="showPass ? 'text' : 'password'"
                          :append-inner-icon="
                            showPass ? 'mdi-eye-off' : 'mdi-eye'
                          "
                          @click:append-inner="showPass = !showPass"
                          variant="outlined"
                          density="comfortable"
                          color="indigo"
                          hide-details="auto"
                        ></v-text-field>
                      </div>

                      <div
                        class="d-flex align-center justify-space-between mb-6"
                      >
                        <v-checkbox
                          v-model="form.rememberMe"
                          label=""
                          color="indigo"
                          density="compact"
                          hide-details
                          class="ms-n2"
                        ></v-checkbox>
                        <router-link
                          to="/Auth/ForgetPassword"
                          class="text-caption text-indigo font-weight-bold text-decoration-none"
                          >  </router-link
                        >
                      </div>

                      <v-btn
                        block
                        color="indigo"
                        size="large"
                        type="submit"
                        class="font-weight-bold rounded-lg elevation-2 mb-4"
                        :loading="loading"
                      >
                        
                      </v-btn>
                      <p v-if="error" class="text-red text-center mb-2">
                        {{ error }}
                      </p>

                      <div class="d-flex align-center mb-4">
                        <v-divider></v-divider>
                        <span class="mx-3 text-caption text-grey"></span>
                        <v-divider></v-divider>
                      </div>

                      <v-btn
                        block
                        variant="outlined"
                        color="grey-darken-2"
                        prepend-icon="mdi-google"
                      >
                          
                      </v-btn>
                    </v-form>
                  </v-col>

                  <v-col
                    cols="12"
                    md="5"
                    class="bg-indigo-darken-3 d-flex flex-column align-center justify-center pa-10 text-center order-1 order-md-2"
                  >
                    <v-img
                      src="https://cdn-icons-png.flaticon.com/512/9187/9187604.png"
                      width="120"
                      class="mb-6 opacity-90 filter-white"
                    ></v-img>

                    <h2 class="text-h4 font-weight-bold text-white mb-2">
                       
                    </h2>
                    <p class="text-indigo-lighten-4 mb-8 text-body-2">
                              
                         .
                    </p>
                    <v-btn
                      variant="flat"
                      color="white"
                      class="text-indigo-darken-3 font-weight-bold rounded-pill px-8"
                      to="/Auth/RegisterNow"
                    >
                        
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-locale-provider>
  </v-app>
</template>

<script setup>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { useAuthStore } from "@/store/auth/LogIn";

const router = useRouter();
const auth = useAuthStore();

const showPass = ref(false);
const error = ref("");
const loading = ref(false);

const form = ref({
  email: "",
  password: "",
  rememberMe: false,
});

const handleLogin = async () => {
  error.value = "";
  loading.value = true;

  setTimeout(() => {
    const result = auth.login(form.value);

    if (!result.success) {
      error.value = result.message;
      loading.value = false;
      return;
    }

    loading.value = false;
    router.push("/"); //  
  }, 1000);
};
</script>

<style scoped>
/*     */
.filter-white {
  filter: brightness(0) invert(1);
}
</style>
