<template>
  <v-app theme="light">
    <v-locale-provider rtl>
      <v-main
        class="bg-grey-lighten-5 d-flex align-center justify-center"
        style="min-height: 100vh"
      >
        <v-container style="margin-top: -100px">
          <v-row justify="center">
            <v-col cols="12" sm="10" md="8" lg="5">
              <v-card class="rounded-xl elevation-5 overflow-hidden">
                <v-progress-linear
                  :model-value="(step / 3) * 100"
                  color="indigo"
                  height="6"
                ></v-progress-linear>

                <v-card-text class="pa-8">
                  <v-window v-model="step">
                    <v-window-item :value="1">
                      <div class="text-center mb-6">
                        <v-avatar
                          color="indigo-lighten-5"
                          size="80"
                          class="mb-4"
                        >
                          <v-icon color="indigo" size="40"
                            >mdi-lock-reset</v-icon
                          >
                        </v-avatar>
                        <h2
                          class="text-h5 font-weight-bold text-indigo-darken-4"
                        >
                            
                        </h2>
                        <p class="text-grey-darken-1 mt-2">
                                   
                           .
                        </p>
                      </div>

                      <v-form @submit.prevent="handleSendCode">
                        <v-text-field
                          v-model="form.emailOrPhone"
                          label="    "
                          prepend-inner-icon="mdi-account-box-outline"
                          variant="outlined"
                          color="indigo"
                          class="mb-4"
                          :rules="[(v) => !!v || '  ']"
                        ></v-text-field>

                        <v-btn
                          block
                          color="indigo"
                          size="large"
                          type="submit"
                          class="rounded-lg font-weight-bold"
                          :loading="loading"
                        >
                           
                        </v-btn>
                      </v-form>
                    </v-window-item>

                    <v-window-item :value="2">
                      <div class="text-center mb-6">
                        <h2
                          class="text-h5 font-weight-bold text-indigo-darken-4"
                        >
                            
                        </h2>
                        <p class="text-grey-darken-1 mt-2">
                               5  : <br />
                          <b class="text-indigo">{{ form.emailOrPhone }}</b>
                        </p>
                      </div>

                      <v-form @submit.prevent="handleVerifyOtp">
                        <div class="d-flex justify-center mb-6">
                          <v-otp-input
                            v-model="form.otp"
                            length="5"
                            variant="outlined"
                            color="indigo"
                            class="otp-field"
                            autofocus
                          ></v-otp-input>
                        </div>

                        <v-btn
                          block
                          color="indigo"
                          size="large"
                          type="submit"
                          class="rounded-lg font-weight-bold mb-4"
                          :loading="loading"
                          :disabled="form.otp.length < 5"
                        >
                            
                        </v-btn>

                        <div class="text-center">
                          <p class="text-caption text-grey">  </p>
                          <v-btn
                            variant="text"
                            color="indigo"
                            size="small"
                            :disabled="timer > 0"
                            @click="resendCode"
                          >
                            {{
                              timer > 0
                                ? `   (${timer}s)`
                                : "  "
                            }}
                          </v-btn>
                        </div>
                      </v-form>
                    </v-window-item>

                    <v-window-item :value="3">
                      <div class="text-center mb-6">
                        <v-avatar
                          color="green-lighten-5"
                          size="80"
                          class="mb-4"
                        >
                          <v-icon color="green" size="40"
                            >mdi-check-decagram</v-icon
                          >
                        </v-avatar>
                        <h2
                          class="text-h5 font-weight-bold text-indigo-darken-4"
                        >
                            
                        </h2>
                        <p class="text-grey-darken-1 mt-2">
                               .
                        </p>
                      </div>

                      <v-form @submit.prevent="handleResetPassword">
                        <v-text-field
                          v-model="form.newPassword"
                          label="  "
                          prepend-inner-icon="mdi-lock-outline"
                          variant="outlined"
                          color="indigo"
                          :type="showPass ? 'text' : 'password'"
                          :append-inner-icon="
                            showPass ? 'mdi-eye-off' : 'mdi-eye'
                          "
                          @click:append-inner="showPass = !showPass"
                          class="mb-2"
                        ></v-text-field>

                        <v-text-field
                          v-model="form.confirmPassword"
                          label="  "
                          prepend-inner-icon="mdi-lock-check-outline"
                          variant="outlined"
                          color="indigo"
                          type="password"
                          class="mb-6"
                          :error-messages="passwordError"
                        ></v-text-field>

                        <v-btn
                          block
                          color="indigo"
                          size="large"
                          type="submit"
                          class="rounded-lg font-weight-bold"
                          :loading="loading"
                        >
                            
                        </v-btn>
                      </v-form>
                    </v-window-item>

                    <v-window-item :value="4">
                      <div class="text-center py-10">
                        <v-icon size="100" color="success" class="mb-4"
                          >mdi-checkbox-marked-circle-outline</v-icon
                        >
                        <h2 class="text-h4 font-weight-bold text-success mb-2">
                           !
                        </h2>
                        <p class="text-grey mb-8">
                                .   
                          .
                        </p>

                        <v-btn
                          to="/Auth/LogIn"
                          color="indigo"
                          size="large"
                          rounded="pill"
                          class="px-10"
                        >
                            
                        </v-btn>
                      </div>
                    </v-window-item>
                  </v-window>
                </v-card-text>

                <v-card-actions
                  v-if="step < 4"
                  class="justify-center bg-grey-lighten-4 py-3"
                >
                  <v-btn
                    variant="plain"
                    color="grey-darken-1"
                    size="small"
                    @click="goBack"
                  >
                    <v-icon start>mdi-arrow-right</v-icon>
                    {{
                      step === 1
                        ? "  "
                        : "  "
                    }}
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-locale-provider>
  </v-app>
</template>

<script setup>
import { ref, reactive, onUnmounted } from "vue";
import { useRouter } from "vue-router";

const router = useRouter();
const step = ref(1);
const loading = ref(false);
const showPass = ref(false);
const passwordError = ref("");
const timer = ref(0);
let intervalId = null;

//  
const form = reactive({
  emailOrPhone: "",
  otp: "",
  newPassword: "",
  confirmPassword: "",
});

// --- Actions ---

// 1.  
const handleSendCode = () => {
  if (!form.emailOrPhone) return;

  loading.value = true;
  //    API
  setTimeout(() => {
    loading.value = false;
    step.value = 2; //   
    startTimer(); //    
  }, 1500);
};

// 2.   
const handleVerifyOtp = () => {
  loading.value = true;
  setTimeout(() => {
    loading.value = false;
    //     
    step.value = 3; //    
  }, 1500);
};

// 3.   
const handleResetPassword = () => {
  passwordError.value = "";

  if (
    form.newPassword !== form.formConfirmPassword &&
    form.newPassword !== form.confirmPassword
  ) {
    if (form.newPassword !== form.confirmPassword) {
      passwordError.value = "   ";
      return;
    }
  }

  loading.value = true;
  setTimeout(() => {
    loading.value = false;
    step.value = 4; //   
  }, 1500);
};

//  
const goBack = () => {
  if (step.value === 1) {
    router.push("/Auth/LogIn"); //   
  } else {
    step.value--; //   
  }
};

//    
const startTimer = () => {
  timer.value = 60;
  clearInterval(intervalId);
  intervalId = setInterval(() => {
    if (timer.value > 0) {
      timer.value--;
    } else {
      clearInterval(intervalId);
    }
  }, 1000);
};

const resendCode = () => {
  //     
  startTimer();
  console.log("Resending code...");
};

onUnmounted(() => {
  clearInterval(intervalId);
});
</script>

<style scoped>
/*    OTP    */
.otp-field :deep(input) {
  text-align: center;
  font-size: 1.5rem;
  letter-spacing: 1rem;
  font-weight: bold;
}
</style>
