<template>
  <v-locale-provider rtl>
    <v-container fluid class="pa-6" dir="rtl">
      <v-card class="mb-6 rounded-xl" elevation="1">
        <v-card-text class="pa-4">
          <div
            class="d-flex align-center justify-space-between flex-wrap gap-4 mb-4"
          >
            <h2
              class="text-h5 font-weight-black"
              style="color: rgb(14, 22, 67)"
            >
               
            </h2>

            <v-btn
              color="primary"
              prepend-icon="mdi-plus"
              class="font-weight-bold px-6 rounded-lg shadow-btn"
              height="44"
              @click="openAddDialog"
            >
               {{ activeTab === "categories" ? "" : "" }}
            </v-btn>
          </div>

          <v-tabs v-model="activeTab" color="primary" class="border-bottom">
            <v-tab value="categories" class="font-weight-bold text-subtitle-1">
              <v-icon right class="ml-2">mdi-shape-outline</v-icon>
               (Categories)
            </v-tab>
            <v-tab value="brands" class="font-weight-bold text-subtitle-1">
              <v-icon right class="ml-2">mdi-tag-multiple-outline</v-icon>
               (Brands)
            </v-tab>
          </v-tabs>
        </v-card-text>
      </v-card>

      <v-window v-model="activeTab">
        <v-window-item value="categories">
          <v-card class="rounded-xl overflow-hidden" elevation="1">
            <v-table class="custom-table pa-2">
              <thead class="bg-grey-lighten-4">
                <tr>
                  <th class="text-right font-weight-bold"> </th>
                  <th class="text-right font-weight-bold"> </th>
                  <th class="text-right font-weight-bold"> </th>
                  <th class="text-right font-weight-bold"></th>
                  <th class="text-center font-weight-bold"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="cat in categories" :key="cat.id" class="hover-row">
                  <td>
                    <v-avatar
                      size="60"
                      rounded="lg"
                      class="elevation-1 my-2 bg-grey-lighten-3"
                    >
                      <v-img :src="cat.image" cover></v-img>
                    </v-avatar>
                  </td>
                  <td class="font-weight-bold text-subtitle-1">
                    {{ cat.name }}
                  </td>
                  <td class="text-grey-darken-1">
                    {{ cat.productsCount }} 
                  </td>
                  <td>
                    <v-switch
                      v-model="cat.isActive"
                      color="success"
                      hide-details
                      density="compact"
                      class="d-inline-flex"
                    ></v-switch>
                  </td>
                  <td class="text-center">
                    <v-btn
                      icon
                      size="small"
                      color="primary"
                      variant="text"
                      @click="editItem(cat)"
                    >
                      <v-icon>mdi-pencil</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      size="small"
                      color="error"
                      variant="text"
                      @click="openDeleteDialog(cat)"
                    >
                      <v-icon>mdi-delete</v-icon>
                    </v-btn>
                  </td>
                </tr>
              </tbody>
            </v-table>
          </v-card>
        </v-window-item>

        <v-window-item value="brands">
          <v-card class="rounded-xl overflow-hidden" elevation="1">
            <v-table class="custom-table pa-2">
              <thead class="bg-grey-lighten-4">
                <tr>
                  <th class="text-right font-weight-bold"> </th>
                  <th class="text-right font-weight-bold"> </th>
                  <th class="text-center font-weight-bold"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="brand in brands" :key="brand.id" class="hover-row">
                  <td>
                    <v-avatar
                      size="60"
                      rounded="lg"
                      class="elevation-1 my-2 bg-white"
                      style="border: 1px solid #eee"
                    >
                      <v-img :src="brand.logo" contain></v-img>
                    </v-avatar>
                  </td>
                  <td class="font-weight-bold text-subtitle-1">
                    {{ brand.name }}
                  </td>
                  <td class="text-center">
                    <v-btn
                      icon
                      size="small"
                      color="primary"
                      variant="text"
                      @click="editItem(brand)"
                    >
                      <v-icon>mdi-pencil</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      size="small"
                      color="error"
                      variant="text"
                      @click="openDeleteDialog(brand)"
                    >
                      <v-icon>mdi-delete</v-icon>
                    </v-btn>
                  </td>
                </tr>
              </tbody>
            </v-table>
          </v-card>
        </v-window-item>
      </v-window>

      <v-dialog v-model="dialog" max-width="500px" persistent>
        <v-card class="rounded-xl pa-2">
          <v-card-title
            class="text-h5 font-weight-bold pt-4 px-4"
            style="color: rgb(14, 22, 67)"
          >
            {{ isEditing ? "" : "" }}
            {{ activeTab === "categories" ? "" : "" }}
          </v-card-title>
          <v-divider class="my-3"></v-divider>

          <v-card-text>
            <v-form ref="form" v-model="validForm">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.name"
                    :label="
                      activeTab === 'categories' ? ' ' : ' '
                    "
                    variant="outlined"
                    color="primary"
                    :rules="[(v) => !!v || ' ']"
                  ></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-file-input
                    v-model="imageFile"
                    :label="
                      activeTab === 'categories'
                        ? '  '
                        : '  '
                    "
                    variant="outlined"
                    color="primary"
                    accept="image/*"
                    prepend-icon=""
                    prepend-inner-icon="mdi-camera"
                    @update:modelValue="handleImageUpload"
                    :rules="[
                      (v) => isEditing || !!previewImage || ' ',
                    ]"
                  ></v-file-input>
                </v-col>

                <v-col
                  cols="12"
                  class="d-flex justify-center"
                  v-if="previewImage"
                >
                  <v-card
                    elevation="2"
                    class="rounded-lg pa-1 bg-grey-lighten-4"
                  >
                    <v-img
                      :src="previewImage"
                      width="120"
                      height="120"
                      :cover="activeTab === 'categories'"
                      :contain="activeTab === 'brands'"
                    ></v-img>
                  </v-card>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>

          <v-card-actions class="pa-4">
            <v-spacer></v-spacer>
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-4 font-weight-bold"
              @click="closeDialog"
              ></v-btn
            >
            <v-btn
              color="primary"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              :disabled="!validForm"
              @click="saveItem"
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="400px">
        <v-card class="rounded-xl pa-4 text-center">
          <v-icon color="error" size="60" class="mb-3"
            >mdi-alert-circle-outline</v-icon
          >
          <v-card-title class="text-h6 font-weight-bold text-wrap">
                
            {{ activeTab === "categories" ? " " : " " }}
          </v-card-title>
          <v-card-actions class="d-flex justify-center mt-4">
            <v-btn
              color="grey-darken-1"
              variant="tonal"
              class="px-6 font-weight-bold"
              @click="dialogDelete = false"
              ></v-btn
            >
            <v-btn
              color="error"
              variant="flat"
              class="px-6 font-weight-bold shadow-btn"
              @click="confirmDelete"
              > </v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-locale-provider>
</template>

<script setup>
import { ref } from "vue";

//   (: )
const activeTab = ref("categories");

//   
const categories = ref([
  {
    id: 1,
    name: "  (Smartphones)",
    image: "https://cdn.dummyjson.com/product-images/1/thumbnail.jpg",
    productsCount: 45,
    isActive: true,
  },
  {
    id: 2,
    name: "  (Laptops)",
    image: "https://cdn.dummyjson.com/product-images/6/thumbnail.png",
    productsCount: 22,
    isActive: true,
  },
  {
    id: 3,
    name: " (Fragrances)",
    image: "https://cdn.dummyjson.com/product-images/11/thumbnail.jpg",
    productsCount: 30,
    isActive: false,
  },
]);

//   
const brands = ref([
  {
    id: 1,
    name: "Apple",
    logo: "https://upload.wikimedia.org/wikipedia/commons/f/fa/Apple_logo_black.svg",
  },
  {
    id: 2,
    name: "Samsung",
    logo: "https://upload.wikimedia.org/wikipedia/commons/2/24/Samsung_Logo.svg",
  },
  {
    id: 3,
    name: "Dell",
    logo: "https://upload.wikimedia.org/wikipedia/commons/1/18/Dell_logo_2016.svg",
  },
]);

//  
const dialog = ref(false);
const dialogDelete = ref(false);
const isEditing = ref(false);
const validForm = ref(false);
const itemToDelete = ref(null);

//  
const imageFile = ref(null);
const previewImage = ref("");

//  
const editedItem = ref({ id: null, name: "", image: "", logo: "" });

//    (   )
const handleImageUpload = (fileArray) => {
  const file = Array.isArray(fileArray) ? fileArray[0] : fileArray;
  if (file) {
    previewImage.value = URL.createObjectURL(file);
    if (activeTab.value === "categories") {
      editedItem.value.image = previewImage.value;
    } else {
      editedItem.value.logo = previewImage.value;
    }
  } else {
    if (!isEditing.value) {
      previewImage.value = "";
      editedItem.value.image = "";
      editedItem.value.logo = "";
    }
  }
};

//   
const openAddDialog = () => {
  isEditing.value = false;
  editedItem.value = {
    id: null,
    name: "",
    image: "",
    logo: "",
    productsCount: 0,
    isActive: true,
  };
  imageFile.value = null;
  previewImage.value = "";
  dialog.value = true;
};

//   
const editItem = (item) => {
  isEditing.value = true;
  editedItem.value = { ...item };
  imageFile.value = null;
  //     
  previewImage.value =
    activeTab.value === "categories" ? item.image : item.logo;
  dialog.value = true;
};

//  
const closeDialog = () => {
  dialog.value = false;
  setTimeout(() => {
    editedItem.value = { id: null, name: "", image: "", logo: "" };
    imageFile.value = null;
    previewImage.value = "";
  }, 300);
};

//  
const saveItem = () => {
  const listToUpdate = activeTab.value === "categories" ? categories : brands;

  if (isEditing.value) {
    const index = listToUpdate.value.findIndex(
      (item) => item.id === editedItem.value.id
    );
    if (index !== -1) listToUpdate.value[index] = { ...editedItem.value };
  } else {
    editedItem.value.id = Date.now();
    listToUpdate.value.unshift({ ...editedItem.value });
  }
  closeDialog();
};

//   
const openDeleteDialog = (item) => {
  itemToDelete.value = item;
  dialogDelete.value = true;
};

//  
const confirmDelete = () => {
  if (activeTab.value === "categories") {
    categories.value = categories.value.filter(
      (cat) => cat.id !== itemToDelete.value.id
    );
  } else {
    brands.value = brands.value.filter(
      (brand) => brand.id !== itemToDelete.value.id
    );
  }
  dialogDelete.value = false;
  itemToDelete.value = null;
};
</script>

