<template>
  <v-app>
    <component :is="currentLayout">
      <router-view />

      <v-snackbar v-model="bar" location="left bottom" max-width="300">
        {{ itemTitle }} has been added to your cart successfully!
        <template v-slot:actions>
          <v-icon @click="bar = false" style="color: #fff">mdi-close</v-icon>
        </template>
      </v-snackbar>
    </component>
  </v-app>
</template>

<script setup>
import { onMounted, ref, inject, computed } from "vue";
import { useRoute } from "vue-router";

import LayOut from "@/components/LayOut.vue";
import DashboardLayout from "@/components/DashboardLayout.vue";

const route = useRoute();
const emitter = inject("emitter");
const bar = ref(false);
const itemTitle = ref("");

const layouts = {
  LayOut,
  DashboardLayout,
};

//   Layout    meta    router
const currentLayoutName = computed(() => {
  return route.meta.layout || "LayOut";
});

const currentLayout = computed(() => {
  return layouts[currentLayoutName.value];
});

onMounted(() => {
  emitter.on("showMessage", (data) => {
    itemTitle.value = data;
    bar.value = true;
  });
});
</script>

