<template>
  <div class="QuickVeiw">
    <v-dialog v-model="QuickVeiw.QuickVeiw" max-width="800">
      <v-card>
        <div class="pt-5" style="position: relative">
          <!-- Close icon -->
          <v-icon
            @click="QuickVeiw.close()"
            size="22"
            style="position: absolute; top: 2px; right: 2px"
          >
            mdi-close
          </v-icon>

          <v-container>
            <!-- Product details -->
            <v-row v-if="productDetails.productDetails?.id">
              <v-col cols="7">
                <img
                  :src="
                    tab.value
                      ? tab.value
                      : productDetails.productDetails.thumbnail
                  "
                  class="w-100 border"
                  height="300px"
                  alt=""
                />

                <v-tabs center-active v-model="tab" height="100px">
                  <v-tab
                    v-for="(img, i) in productDetails.productDetails.images"
                    :key="i"
                    class="mx-10"
                    :value="img"
                  >
                    <img :src="img" alt="not found" width="" height="220" />
                  </v-tab>
                </v-tabs>
              </v-col>

              <v-col cols="5">
                <v-card class="text-justify">
                  <v-card-title style="font-weight: 600">{{
                    productDetails.productDetails.title
                  }}</v-card-title>
                  <v-card-text>{{
                    productDetails.productDetails.description
                  }}</v-card-text>
                  <v-rating
                    class="py-1"
                    v-model="productDetails.productDetails.rating"
                    readonly
                    color="yellow-darken-2"
                    half-increments
                  ></v-rating>
                  <v-card-text>
                    <span style="font-weight: 600">Stock:</span>
                    {{
                      productDetails.productDetails.stock > 0
                        ? productDetails.productDetails.stock
                        : "No Stock"
                    }}
                  </v-card-text>
                  <v-card-text v-if="productDetails.productDetails.sale">
                    <span style="font-weight: 600">Sale:</span>

                    {{
                      productDetails.productDetails.sale
                        ? productDetails.productDetails.sale
                        : "No sale"
                    }}
                  </v-card-text>
                  <v-card-text>
                    <span style="font-weight: 600">Brand:</span>
                    {{
                      productDetails.productDetails.brand
                        ? productDetails.productDetails.brand
                        : "No brand"
                    }}
                  </v-card-text>
                  <v-card-text>
                    <del>
                      <span style="font-weight: 600">Price:</span> ${{
                        productDetails.productDetails.price
                      }}</del
                    >
                    From
                    {{
                      Math.ceil(
                        productDetails.productDetails.price -
                          productDetails.productDetails.price *
                            (productDetails.productDetails.discountPercentage /
                              100)
                      )
                    }}
                  </v-card-text>

                  <div class="py-2 px-3">
                    <v-icon size="22" @click="decrease">mdi-minus</v-icon>
                    <input
                      type="number"
                      class="text-center w-25"
                      v-model="quantity"
                    />
                    <v-icon size="22" @click="increase">mdi-plus</v-icon>
                  </div>
                  <v-card-text>
                    <span style="font-weight: 600">Total Price:$</span>

                    {{
                      Math.ceil(
                        productDetails.productDetails.price -
                          productDetails.productDetails.price *
                            (productDetails.productDetails.discountPercentage /
                              100)
                      ) * quantity
                    }}</v-card-text
                  >
                  <v-card-actions>
                    <v-btn
                      color="white"
                      style="
                        background-color: black;
                        border-radius: 30px;
                        height: 50px;
                      "
                      variant="outlined"
                      block
                      :loading="loading"
                      :disabled="loading"
                      @click="AddToCart(productDetails.productDetails)"
                    >
                      Add to cart
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>

            <!-- Loader -->
            <v-skeleton-loader v-else type="image, card" />
          </v-container>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script setup>
import { ref, watch } from "vue";
import { usePopUpQuickView } from "@/store/PopUp";
import { ProductModule } from "@/store/Products";
import { AddInCart } from "@/store/Cart";
const QuickVeiw = usePopUpQuickView();
const productDetails = ProductModule();
const CartItem = AddInCart();
const loading = ref(false);
import { inject } from "vue";
const emitter = inject("emitter");
const AddToCart = (item) => {
  loading.value = true;

  const product = { ...item, quantity: quantity.value };

  setTimeout(() => {
    CartItem.AddInCartItem(product);
    loading.value = false;
    QuickVeiw.close();
    emitter.emit("toggleCart");
    emitter.emit("showMessage", product.title);
  }, 1000); // 1 second
};

const tab = ref("");
const quantity = ref(1);
const increase = () => quantity.value++;
const decrease = () => {
  if (quantity.value > 1) quantity.value--;
};
watch(
  () => QuickVeiw.productId,
  async (id) => {
    if (id) {
      // reset tab & quantity for new product
      productDetails.productDetails = null;
      tab.value = "";
      quantity.value = 1;
      setTimeout(async () => {
        await productDetails.getSingleProducts(id);
      }, 1000);
    }
  }
);
</script>
