import { defineStore } from "pinia";

export const usePopUpQuickView = defineStore("usePopUpQuickView", {
  state: () => ({
    QuickVeiw: false,
    productId: null,
  }),
  actions: {
    open(id) {
      this.productId = id;
      this.QuickVeiw = true;
    },
    close() {
      this.QuickVeiw = false;
      this.productId = null;
    },
  },
});
