<script setup>
import { ref, onMounted, onUnmounted, nextTick, inject } from "vue";
// import { storeToRefs } from "pinia";

const emitter = inject("emitter");
// const productStore = ProductModule();
const menuItems = ref([
  { text: "", link: "/", badge: null },
  { text: "", link: "/User/ProductsPage", badge: null },

  {
    text: " ",
    link: null,
    badge: null,
    children: [
      { text: "Electronics", link: "/User/category/electronics" },
      { text: "Flash Deal", link: "/User/category/ProductPage" },
      { text: "Home & Garden", link: "/User/category/home-garden" },
    ],
  },
  // ---------------------------------------

  {
    text: " ",
    link: null,
    badge: null,
    children: [
      { text: "", link: "/User/ControlPannel/StatisticsPage" },
      { text: "", link: "/User/ControlPannel/OrdersPage" },
      {
        text: "  ",
        link: "/User/ControlPannel/CommissionTransferRequests",
      },
      { text: "", link: "/User/ControlPannel/ElhesapatePage" },
      { text: " ", link: "/User/ControlPannel/AddModeratorPage" },
    ],
  },
  { text: " ", link: "/User/AddOrder", badge: null },
]);
const mobileMenu = ref(false);
const searchOpen = ref(false);
const isScrolled = ref(false);
const searchInput = ref(null);

// --- User Data ---
const userData = ref({
  name: " ",
  avatar: "https://i.pravatar.cc/150?u=a042581f4e29026704d",
});

// --- Dynamic Notification Data ---
const notificationList = ref([
  {
    title: "Order Shipped",
    subtitle: "Order #1234 has been shipped",
    icon: "mdi-truck-delivery",
    color: "blue",
    time: "2m",
  },
  {
    title: "New Sale!",
    subtitle: "50% off on electronics",
    icon: "mdi-tag",
    color: "red",
    time: "1h",
  },
  {
    title: "Account Secure",
    subtitle: "Password changed successfully",
    icon: "mdi-shield-check",
    color: "green",
    time: "1d",
  },
]);

// --- Dynamic Messages Data ---
const messageList = ref([
  {
    from: "Ahmed Ali",
    message: "Hey, is this available?",
    avatar: "https://i.pravatar.cc/150?u=1",
    time: "5m",
  },
  {
    from: "Support Team",
    message: "Your ticket #990 resolved.",
    avatar: "https://i.pravatar.cc/150?u=2",
    time: "2h",
  },
  {
    from: "Sara Smith",
    message: "Thanks for the review!",
    avatar: "https://i.pravatar.cc/150?u=3",
    time: "1d",
  },
]);

// --- Language Logic ---
const availableLangs = [
  { code: "En", name: "English", flag: "https://flagcdn.com/w40/us.png" },
  { code: "AR", name: "", flag: "https://flagcdn.com/w40/eg.png" },
];
const currentLang = ref(availableLangs[0]); // Default EN

const changeLang = (lang) => {
  currentLang.value = lang;
  // Here you can add your i18n logic
  console.log(`Language changed to ${lang.name}`);
};

// Methods
const toggleCart = () => emitter.emit("toggleCart");
const toggleSearch = async () => {
  searchOpen.value = !searchOpen.value;
  if (searchOpen.value) {
    await nextTick();
    searchInput.value?.focus();
  }
};
const handleScroll = () => {
  isScrolled.value = window.scrollY > 10;
};

// Lifecycle
onMounted(() => window.addEventListener("scroll", handleScroll));
onUnmounted(() => window.removeEventListener("scroll", handleScroll));
</script>

