<template>
  <v-container class="py-8" dir="rtl">
    <v-row justify="center">
      <v-col cols="12" md="8" lg="6">
        <v-card elevation="3" class="rounded-xl overflow-hidden">
          <v-toolbar color="#0E1643" class="text-white px-2">
            <v-icon class="mr-2">mdi-bell-ring-outline</v-icon>
            <v-toolbar-title class="font-weight-bold"
              ></v-toolbar-title
            >
            <v-spacer></v-spacer>
            <v-tooltip location="bottom">
              <template v-slot:activator="{ props }">
                <v-btn
                  icon
                  variant="text"
                  v-bind="props"
                  @click="markAllAsRead"
                  :disabled="unreadCount === 0"
                >
                  <v-icon>mdi-check-all</v-icon>
                </v-btn>
              </template>
              <span>  </span>
            </v-tooltip>
          </v-toolbar>

          <v-list lines="three" class="bg-surface pa-0">
            <template v-for="(note, index) in notifications" :key="note.id">
              <v-list-item
                :value="note.id"
                class="pa-4 cursor-pointer transition-fast-in-fast-out"
                :class="{
                  'bg-blue-grey-lighten-5': note.isNew,
                  'hover-bg': true,
                }"
                @click="openNotification(note)"
              >
                <template v-slot:prepend>
                  <v-avatar
                    size="48"
                    :color="note.color + '-lighten-4'"
                    class="ml-4"
                  >
                    <v-icon :color="note.color">{{ note.icon }}</v-icon>
                  </v-avatar>
                </template>

                <v-list-item-title
                  class="d-flex justify-space-between align-start mb-1"
                >
                  <span
                    class="text-subtitle-1"
                    :class="{
                      'font-weight-bold text-black': note.isNew,
                      'font-weight-medium text-grey-darken-2': !note.isNew,
                    }"
                  >
                    {{ note.title }}
                  </span>
                  <span
                    class="text-caption text-grey-darken-1 whitespace-nowrap mr-2"
                    style="white-space: nowrap"
                  >
                    {{ note.time }}
                  </span>
                </v-list-item-title>

                <v-list-item-subtitle
                  class="text-body-2"
                  :class="{
                    'text-black': note.isNew,
                    'text-grey': !note.isNew,
                  }"
                >
                  {{ note.description }}
                </v-list-item-subtitle>

                <template v-slot:append v-if="note.isNew">
                  <v-badge dot color="error" class="mr-2 mt-2"></v-badge>
                </template>
              </v-list-item>

              <v-divider v-if="index < notifications.length - 1"></v-divider>
            </template>
          </v-list>

          <div
            v-if="notifications.length === 0"
            class="text-center pa-10 text-grey"
          >
            <v-icon size="60" color="grey-lighten-2" class="mb-4"
              >mdi-bell-off-outline</v-icon
            >
            <h3>   </h3>
            <p class="text-caption mt-2">     .</p>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script setup>
import { ref, computed } from "vue";

//    (Mock Data)
const notifications = ref([
  {
    id: 1,
    title: "   ! ",
    description:
      "  #45892    .       .",
    time: " 10 ",
    icon: "mdi-truck-fast-outline",
    color: "success", //  
    isNew: true,
  },
  {
    id: 2,
    title: " 20%  ",
    description:
      "  ZAD20      20%    .    24 .",
    time: "",
    icon: "mdi-gift-outline",
    color: "purple", //  
    isNew: true,
  },
  {
    id: 3,
    title: " :   ",
    description:
      "        (Windows 11)   .         .",
    time: " 08:30 ",
    icon: "mdi-shield-alert-outline",
    color: "error", //   
    isNew: false,
  },
  {
    id: 4,
    title: " ",
    description:
      '    "  "       .',
    time: "15 ",
    icon: "mdi-star-outline",
    color: "warning", // / 
    isNew: false,
  },
]);

//      /  "  "
const unreadCount = computed(() => {
  return notifications.value.filter((note) => note.isNew).length;
});

//        
const openNotification = (note) => {
  note.isNew = false;
  //         
  // : if (note.type === 'order') router.push('/user/orders')
};

//       
const markAllAsRead = () => {
  notifications.value.forEach((note) => {
    note.isNew = false;
  });
};
</script>

<style scoped>
.hover-bg:hover {
  background-color: #f5f5f5 !important;
}
/*       */
.bg-blue-grey-lighten-5 {
  background-color: #f8fafc !important;
}
</style>
