<script setup>
import { ref, computed } from "vue";

const search = ref("");

//   
const moderators = ref([
  {
    id: 1,
    name: " ",
    initials: "AM",
    email: "ahmed@company.com",
    role: "Admin",
    date: "2023-01-15",
    isActive: true,
  },
  {
    id: 2,
    name: " ",
    initials: "SA",
    email: "sara.ali@company.com",
    role: "Moderator",
    date: "2023-05-20",
    isActive: true,
  },
  {
    id: 3,
    name: " ",
    initials: "KH",
    email: "kareem@company.com",
    role: "Support",
    date: "2023-08-10",
    isActive: false,
  },
  {
    id: 4,
    name: " ",
    initials: "MS",
    email: "mona@company.com",
    role: "Accountant",
    date: "2023-11-01",
    isActive: true,
  },
]);

//    (   )
const filteredModerators = computed(() => {
  if (!search.value) return moderators.value;
  const q = search.value.toLowerCase();
  return moderators.value.filter(
    (item) =>
      item.name.toLowerCase().includes(q) || item.role.toLowerCase().includes(q)
  );
});

//    
const getRoleColor = (role) => {
  switch (role) {
    case "Admin":
      return "purple-darken-1";
    case "Moderator":
      return "indigo";
    case "Support":
      return "teal";
    case "Accountant":
      return "amber-darken-3";
    default:
      return "grey";
  }
};
</script>

